import moment from 'moment';
import _ from 'lodash';

export default class CarInfoConfig {
    static carInfoConfig = [
        {
            key: 'name',
            label: '品名',
            excelKey: 'Model',
        },
        {
            key: 'brand',
            label: '品牌',
            excelKey: '品牌',
        },
        {
            key: 'specification',
            label: '规格',
            excelKey: '规格',
        },
        {
            key: 'model',
            label: '型号',
            excelKey: 'Model',
        },
        {
            key: 'vId',
            label: '底盘号',
            excelKey: '底盘号',
        },
        {
            key: 'eId',
            label: '发动机号',
            excelKey: '发动机号',
        },
        {
            key: 'color',
            label: '外色',
            excelKey: '外色',
        },
        {
            key: 'weight',
            label: '重量（千克）',
            excelKey: 'Gross Weight (Kgs)',
        },
        {
            key: 'displacement',
            label: '排量',
            excelKey: '排量',
        },
        {
            key: 'productionDate',
            label: '生产日期',
            excelKey: '生产日期',
            resolveInitialValue: iValue => {
                if (iValue) {
                    return moment('1900')
                        .add(iValue, 'days')
                        .format('YYYY-MM-DD');
                }

                return iValue;
            },
        },
        {
            key: 'taxId',
            label: '税号',
            excelKey: '税号',
        },
        {
            key: 'customsClearanceAmount',
            label: '报关金额',
            excelKey: '报关价格',
        },
        {
            key: 'insteadCollectionAndPaymentAmount',
            label: '代收代付金额',
            excelKey: '代收代付金额',
        },
        {
            key: 'parkingLocation',
            label: '车辆停放位置',
            excelKey: '车辆停放位置',
        },
        {
            key: 'inboundDate',
            label: '入库日期',
            excelKey: '入库日期',
            resolveInitialValue: iValue => {
                if (iValue) {
                    return moment('1900')
                        .add(iValue, 'days')
                        .format('YYYY-MM-DD');
                }

                return iValue;
            },
        },
        {
            key: 'outboundDate',
            label: '出库日期',
            excelKey: '出库日期',
            resolveInitialValue: iValue => {
                if (iValue) {
                    return moment('1900')
                        .add(iValue, 'days')
                        .format('YYYY-MM-DD');
                }

                return iValue;
            },
        },
        {
            key: 'carStyle',
            label: '车款',
            excelKey: '车款',
        },
        {
            key: 'BL',
            label: 'B/L',
            excelKey: 'B/L',
        },
        {
            key: 'capacity',
            label: 'Capacity (c.c)',
            excelKey: 'Capacity (c.c)',
            resolveInitialValue: iValue => {
                if (iValue) {
                    const value = parseFloat(iValue);
                    return _.isNaN(value) ? '' : value.toFixed(3);
                }

                return iValue;
            },
        },
    ];
}



