<template>
    <div class="car-info-detail-key-value">
        <div class="label">
            <pt-title type="text" size="14">
                {{ label }}:
            </pt-title>
        </div>
        <div class="value">
            <pt-title v-if="!isEdit" :important="!!value" :ignore="!value"
                :format="valueFormat" :size="valueSize" :class="csvFieldType ? 'underscore' : ''"
                type="text">
                <span @click="onValueClick">{{ processedValue }}</span>
            </pt-title>
            <pt-input v-else v-model="value" :input-type="field.inputType"
                :validate="validator" :tooltip="validator.message" size="small" />
        </div>
    </div>
</template>

<script>
import _ from 'lodash';
import moment from 'moment';

export default {
    name: 'CarInfoDetailKeyValue',
    props: {
        carInfo: { type: Object, required: true },
        field: { type: Object, required: true },
        isEdit: { type: Boolean, required: false, default: false },
        validator: { type: Object, required: true },
    },
    computed: {
        csvFieldType: {
            get() {
                return this.field.fieldType === 'CSV';
            },
        },
        numberValue: {
            get() {
                return this.field.fieldType === Number;
            },
        },
        dateValue: {
            get() {
                return this.field.fieldType === Date;
            },
        },
        label: {
            get() {
                return this.field.label;
            },
        },
        value: {
            get() {
                const value = this.carInfo.getValue(this.field.name);

                if (this.csvFieldType) {
                    return _.defaultTo(_.first(value.split(',')), '');
                }

                if (
                    this.numberValue &&
                    value &&
                    `${Number.parseFloat(value)}`
                ) {
                    return Number.parseFloat(value).toFixed(3);
                }

                if (this.dateValue && /^\d{5}(.\d{3})?$/.test(value)) {
                    return moment('1900')
                        .add(value, 'days')
                        .format('YYYY-MM-DD');
                }

                return value;
            },
            set(value) {
                this.carInfo[this.field.name] = value.trim();
            },
        },
        processedValue: {
            get() {
                return this.value ? this.value : `暂无${this.label}`;
            },
        },
        valueFormat: {
            get() {
                if (this.value) {
                    if (this.csvFieldType) {
                        return 'number';
                    }

                    return '';
                }

                return 'date';
            },
        },
        valueSize: {
            get() {
                return this.value ? '18' : '16';
            },
        },
    },
    methods: {
        onValueClick() {
            this.$emit('valueClick', this.carInfo, this.field);
        },
    },
};
</script>

<style lang="scss">
.car-info-detail-key-value {
    flex: none;
    display: flex;
    flex-flow: row wrap;
    // padding: 0 36px 0 0;
    margin: 0 36px 5px 0;
    max-width: 100%;
    overflow: hidden;

    .label,
    .value {
        line-height: 40px;
    }

    .label {
        flex: none;
        width: 140px;
        padding-left: 20px;
        box-sizing: border-box;
    }

    .value {
        flex: none;
        width: 184px;
        overflow: hidden;
    }

    .underscore {
        cursor: pointer;
    }
}
</style>



