import _ from 'lodash';
import moment from 'moment';

const defaultOptions = {
    numberPrecision: 3,
    defaultValue: '',
    contentWidth: '1',
};

export default class Field {
    constructor(name, label, excelKey, fieldType, tags, options) {
        this._name = name;
        this._label = label;
        this._excelKey = excelKey;
        this._fieldType = fieldType;
        this._options = _.chain({})
            .assign(defaultOptions)
            .assign(options)
            .value();
        this._tags = tags || {};
        this._parent = {};
    }

    get parent() {
        return this._parent;
    }

    set parent(value) {
        this._parent = value;
    }

    get name() {
        return this._name;
    }

    get label() {
        return this._label;
    }

    get excelKey() {
        return this._excelKey;
    }

    get fieldType() {
        return this._fieldType;
    }

    get options() {
        return this._options;
    }

    get tags() {
        return this._tags;
    }

    get type() {
        return 'default';
    }

    get inputType() {
        return this.fieldType === Date ? 'date' : 'input';
    }

    findField(name) {
        return this.name === name ? this : {};
    }

    getOption(name, defaultValue) {
        return _.defaultTo(this.options[name], defaultValue);
    }

    hasTags(tag) {
        const tags = _.isArray(tag) ? tag : [tag];
        return _.every(tags, p => _.some(this.tags, t => t === p));
    }

    formatValue(value, context) {
        if (this.fieldType !== Boolean) {
            if (_.isUndefined(value) || _.isEmpty(value) || _.isNull(value)) {
                return this.options.defaultValue;
            }
        }

        switch (this.fieldType) {
            case Number: {
                const numeric = parseFloat(value);
                return _.isNaN(numeric) ? '' : numeric.toFixed(3);
            }
            case Date: {
                if (/^\d{5}(.\d{3})?$/.test(value)) {
                    return moment('1900')
                        .add(value, 'days')
                        .format('YYYY-MM-DD');
                }
                break;
            }
            case Boolean: {
                if (!_.isEmpty(this.parent)) {
                    const bitValue = this.getOption('bitValue', 0);
                    value = (context.getValue(this.parent.name) & bitValue) === bitValue;
                }

                if (value) {
                    return this.getOption('trueValue', `${this.label}:是`);
                }
                return this.getOption('falseValue', `${this.label}:否`);
            }
            default: {
                break;
            }
        }

        return value;
    }

    getDefaultValue() {
        switch (this.fieldType) {
            case Number: {
                return 0;
            }
            case Date: {
                return moment().format('YYYY-MM-DD');
            }
            case Boolean: {
                return false;
            }
            case String:
            default: {
                return '';
            }
        }
    }

    defineCarProperty(car, data, business) {
        const field = this;
        data[this.name] = _.defaultTo(data[this.name], this.getDefaultValue());
        Object.defineProperty(car, this.name, {
            set(value) {
                data[field.name] = value;
            },
            get() {
                return business.formatValue ? field.formatValue(data[field.name], car) : car.getValue(field.name);
            },
        });
    }

    defineValidator(validator) {
        const $this = this;

        validator[this.name] = function() {
            const required = $this.getOption('required', false);
            if (required && !this.required(`${$this.label}不能为空`)) {
                return false;
            }

            if ($this.fieldType === Number && !this.numeric('必须为数字')) {
                return false;
            }

            const regex = $this.getOption('regexp', false);
            if (regex && !this.must(p => regex.test(p), $this.getOption('regexErrorMessage', '格式错误'))) {
                return false;
            }

            const minLength = $this.getOption('minLength', false);
            if (minLength && !this.must(p => `${p}`.length >= minLength, `最小长度为${minLength}字符`)) {
                return false;
            }

            const maxLength = $this.getOption('maxLength', false);
            if (maxLength && !this.must(p => `${p}`.length <= maxLength, `最大长度为${maxLength}字符`)) {
                return false;
            }

            return true;
        };
    }
}



