<template>
    <div>
        <el-upload class="upload-box upload-demo" ref="upload" :action="fileUrl"
            :http-request="upload" :data="uploadData" :headers="headers"
            :on-success="handleSuccess" :show-file-list="false">
            <el-button size="mini" :type="buttonType" @click="createResource">{{text}}</el-button>
        </el-upload>
    </div>
</template>

<script>
import _ from 'lodash';
import putUpload from '../../utils/put-upload';
import ResourceUtil from '../../api/module-resource/resource-util';
import ExcelUtil from '../../utils/excel-utils';

export default {
    name: 'CarExcelImporter',
    data() {
        return {
            fileUrl: '',
            uploadData: {
                dataName: 'excel',
            },
            excelUid: '',
            operationToken: '',
        };
    },
    props: {
        business: { type: Object, required: true },
        text: { type: String, default: '导入车辆' },
        buttonType: { type: String, default: 'primary' },
    },
    computed: {
        headers() {
            return {
                operationToken: this.operationToken,
                'Ocp-Apim-Subscription-Key': 'ad91c2f95eab4af7aa1efe4adb9a6e60',
            };
        },
    },
    methods: {
        createResource() {
            this.loadResource().then(result => {
                this.operationToken = result.operationToken;
                this.excelUid = result.uid;
                this.fileUrl = `${serverApiConfig.resource}/v1/${this.excelUid}`;
            });
        },
        loadResource() {
            return ResourceUtil.createOneResource('');
        },
        upload(item) {
            return putUpload(item);
        },
        async handleSuccess() {
            let carsListFromExcel = [];

            const result = await ResourceUtil.queryResourceContent(this.excelUid, 'excel', {
                overrideContentType: 'application/excel-2-json',
            });

            carsListFromExcel = ExcelUtil.parse(result);

            const rowMax = carsListFromExcel.defaultSheet.getMaxRows();
            const carObjsFromExcel = [];
            for (let i = 1; i <= rowMax; i++) {
                const obj = carsListFromExcel.defaultSheet.getRowAsObject(i);
                const values = _.values(obj);
                if (!_.every(values, r => r.isEmptyValue())) {
                    carObjsFromExcel.push(this.business.createCarLine(obj, true));
                }
            }
            this.$emit('importCompleted', carObjsFromExcel);
        },
    },
};
</script>

<style scoped>
</style>



