<template>
    <div class="layout-menu">
        <el-menu :default-active="defaultActive" router class="el-menu-vertical-demo">
            <template v-for="item in leftMenuConfig">
                <el-submenu :index="item.index" :key="item.index">
                    <template slot="title">
                        <i class="el-icon-location"></i>
                        <span slot="title">{{ item.title }}</span>
                    </template>
                    <template v-for="child in item.children">
                        <el-menu-item :index="child.index" :route="{path: child.path }"
                            :key="child.index">
                            {{ child.title }}
                        </el-menu-item>
                    </template>
                </el-submenu>
            </template>
        </el-menu>
    </div>
</template>

<script>
import LeftMenuConfig from '../config/menu-config';

export default {
    name: 'LayoutMenu',
    data() {
        return {
            leftMenuConfig: LeftMenuConfig,
            isCollapse: true,
        };
    },
    computed: {
        defaultActive: {
            get() {
                const path = this.$route.path;

                if (path.includes('/carsInfoList')) {
                    return '1-1';
                } else if (path.includes('/receipt-list')) {
                    return '1-2';
                }

                return '1-1';
            },
        },
    },
};
</script>

<style lang="scss" scoped>
.layout-menu {
    flex: 1;
    border: solid 1px #e6e6e6;
    .el-menu {
        border-right: none;
    }
}
</style>



