import _ from 'lodash';

export default class CarLine {
    constructor(car, refCar, validator) {
        this._car = car;
        this._refCar = refCar;
        this._isSelect = false;
        this._isChecked = false;
        this._status = -2;
        this._dataMirror = {};
        this._validator = _.defaultTo(validator, {});
        this._confirmCheck = false;
    }

    set confirmCheck(val) {
        this._confirmCheck = val;
    }
    get confirmCheck() {
        return this._confirmCheck;
    }

    get car() {
        return this._car;
    }

    get isSelect() {
        return this._isSelect;
    }
    set isSelect(value) {
        this._isSelect = value;
    }

    get isChecked() {
        return this._isChecked;
    }
    set isChecked(value) {
        this._isChecked = value;
    }

    get status() {
        return this._status;
    }
    set status(value) {
        this._status = value;
    }

    get statusText() {
        switch (this.status) {
            case 0:
                return '重复';
            case -1:
                return '新增';
            case 1:
                return '覆盖';
            case 2:
                return '无效车辆信息';
            case 3:
                return '已完成此业务';
            case 4:
                return '未完成入库';
            default:
                return '';
        }
    }

    get validator() {
        return this._validator;
    }

    onEdit = () => {
        this._dataMirror = _.cloneDeep(this.car._data);
    };

    onEditCancel = () => {
        this.car._data = this._dataMirror;
    };

    onEditConfirm = () => {};
}



