<template>
    <el-autocomplete v-model="text" :fetch-suggestions="onSearch" @select="onSelect"
        placeholder="输入搜索关键字（至少三位字符）" value-key="keyword">
        <template slot-scope="{ item }">
            <search-item-vid-vid v-if="item.type=='vId' && item.returnType=='vId'"
                :item="item" :keyword="text"></search-item-vid-vid>
            <search-item-eid-vid v-else-if="item.type=='eId' && item.returnType=='vId'"
                :item="item" :keyword="text"></search-item-eid-vid>
            <search-item-note-vids v-else-if="item.type==='note' && item.returnType=='vIds'"
                :item="item" :keyword="text"></search-item-note-vids>
            <search-item-receiptid-title-vids v-else-if="item.type === 'receipt' && item.returnType === 'vIds'"
                :item="item" :keyword="text">
            </search-item-receiptid-title-vids>
            <search-item-vid-eid-title-receiptid-receiptid v-if="item.returnType === 'receipt' || item.returnType === 'outbound' || item.returnType === 'inbound'"
                :item="item" :keyword="text">
            </search-item-vid-eid-title-receiptid-receiptid>
        </template>
    </el-autocomplete>
</template>

<script>
import _ from 'lodash';
import CarInfoApi from '../../api/module-car-info/car-info-api';

export default {
    name: 'SearchInput',
    props: {
        types: { type: Array, required: true },
        expectedTypes: { type: Array, required: true },
        minLength: { type: Number, default: 3 },
        mode: { type: String, default: 'cars' },
    },
    data() {
        return {
            text: '',
            // testNoteData: [
            //     {
            //         type: 'note',
            //         keyword: '我是单试导入单',
            //         result: ['ZFF82YNE4J0236539', 'ZFF82YNE4J0236510'],
            //         returnType: 'vIds',
            //     },
            //     {
            //         type: 'note',
            //         keyword: '我是单试导入单2',
            //         result: ['ZFF82YNE4J0236539', 'ZFF82YNE4J0236520'],
            //         returnType: 'vIds',
            //     },
            // ],
        };
    },
    computed: {
        carMode() {
            return this.mode === 'cars';
        },
        receiptModel() {
            return this.mode === 'receipt';
        },
    },
    methods: {
        hasType(type) {
            return _.some(this.types, p => p === type);
        },
        onSearch(text, cb) {
            const processedText = text.trim();

            if (processedText.length < this.minLength) {
                cb([]);
                return;
            }
            const returnType = this.expectedTypes.join(',');
            const types = this.types.join(',');
            CarInfoApi.getCarsAndReceiptList(processedText, returnType, types).then(p => cb(p));
        },
        onSelect(item) {
            this.$emit('select', item);
        },
    },
};
</script>

<style lang="scss" scoped>
.search-item {
    display: flex;
}
</style>



