import carsInfoList from './components/cars-info-list';
import carsInfoListItem from './components/cars-info-list-item';
import carInfoDetail from './components/car-info-detail';
import carsInsertExcelDialogList from './components/cars-insert-excel-dialog-list';
import carInfoDetailKeyValue from './components/car-info-detail-key-value';
import carFieldValue from './components/car-field-value';
import carListDialog from './components/car-list-dialog';
import carExcelImporter from './components/car-excel-importer';

const install = Vue => {
    Vue.component(carsInfoList.name, carsInfoList);
    Vue.component(carsInfoListItem.name, carsInfoListItem);
    Vue.component(carInfoDetail.name, carInfoDetail);
    Vue.component(carsInsertExcelDialogList.name, carsInsertExcelDialogList);
    Vue.component(carInfoDetailKeyValue.name, carInfoDetailKeyValue);
    Vue.component(carFieldValue.name, carFieldValue);
    Vue.component(carListDialog.name, carListDialog);
    Vue.component(carExcelImporter.name, carExcelImporter);
};

export default {
    install,
};



