<template>
    <navigate-layout>
        <template slot="header">
            <pt-title type="title" size="4">{{ title }}</pt-title>
        </template>
        <template slot="middle-area">
            <search-input :types="['vId','eId', 'receipt' ]" :expectedTypes="['receipt']"
                mode="receipt" @select="onSelect" ref="searchInput" class="search-input">
            </search-input>
            <el-button type="primary" size="small" @click="reset">重置</el-button>
        </template>
        <template slot="right-area">
            <el-dropdown>
                <el-button type="primary" size="small">
                    导入单据
                    <i class="el-icon-arrow-down el-icon--right"></i>
                </el-button>
                <el-dropdown-menu slot="dropdown">
                    <car-excel-importer text="导入进口报关单" @importCompleted="onImported(inboundCustomsDeclarationBusiness, $event)"
                        button-type="text" :business="inboundCustomsDeclarationBusiness"></car-excel-importer>
                    <car-excel-importer text="导入进口证明书" :business="inboundCertificateReceiptBusiness"
                        @importCompleted="onImported(inboundCertificateReceiptBusiness, $event)"
                        button-type="text"></car-excel-importer>
                </el-dropdown-menu>
                <car-list-dialog :manager="receiptManager" :visible.sync="insertDialog.show"
                    :business="insertDialog.business" :fields="insertDialog.listFields"
                    :lines="insertDialog.lines" @dialogOk="confirmDialog" :mode="insertDialog.mode"
                    show-status>
                </car-list-dialog>
            </el-dropdown>
        </template>
        <div slot="content" class="document-list">
            <div class="document-list-top">
            </div>
            <div class="document-list-main">
                <div v-if="receiptList.length === 0" class="no-document">
                    暂无文档
                </div>
                <document-list-item v-for="item in receiptList" :key="item.id"
                    :item="item" @deleteDocument="handleDeleteReceipt"
                    @goDetail="goDetail" />
            </div>
        </div>
    </navigate-layout>
</template>

<script>
import _ from 'lodash';
import ReceiptManager from '../js/receipt-manager';
import Configuration from '../../module-car-info/config/configuration';
import CarInfoApi from '../../api/module-car-info/car-info-api';
import CarLine from '../../module-car-info/models/car-line';
import ResourceStore from '../js/resource-store';

export default {
    name: 'ReceiptList',
    data() {
        return {
            receiptManager: ReceiptManager.instance,
            inboundCustomsDeclarationBusiness: Configuration.InboundCustomsDeclaration,
            inboundCertificateReceiptBusiness: Configuration.InboundCertificateReceipt,
            insertDialog: {
                show: false,
                lines: [],
                remoteLines: [],
                mode: 'carsExcel',
                business: {},
                listFields: [],
            },
            resourceStore: new ResourceStore(),
        };
    },
    created() {
        this.init();
    },
    computed: {
        title: {
            get() {
                return '单据列表';
            },
        },
        receiptList: {
            get() {
                return this.receiptManager ? this.receiptManager.receiptList : [];
            },
        },
    },
    methods: {
        init() {
            ReceiptManager.createInstance()
                .loadReceiptList()
                .then(() => {
                    this.receiptManager = ReceiptManager.instance;
                });
        },
        async onImported(business, lines) {
            const carObjIds = _.map(lines, item => item.car.vId);
            const fetchCarsResult = await CarInfoApi.fetchCars(carObjIds);
            const remoteLines = _.map(fetchCarsResult, p => new CarLine(business.createCar(p), {}, {}));
            _.each(lines, l => {
                if (!_.some(remoteLines, r => r.car.vId === l.car.vId)) {
                    l.status = 2;
                } else {
                    if (_.some(remoteLines, r => r.car.vId === l.car.vId && r.car.status === '0')) {
                        l.status = 4;
                    }

                    const expectedStatus = business.getOption('statusValue', 0);
                    if (expectedStatus > 0) {
                        if (
                            _.some(
                                remoteLines,
                                r =>
                                    r.car.vId === l.car.vId &&
                                    !_.isEmpty(r.car.status) &&
                                    (r.car.status & expectedStatus) === expectedStatus,
                            )
                        ) {
                            l.status = 3;
                        }
                    }
                }
            });

            this.insertDialog.remoteLines = remoteLines;
            this.insertDialog.business = business;
            this.insertDialog.listFields = business.getOrderedFieldsByOption('listOrders', Infinity);
            this.insertDialog.lines = lines;
            this.insertDialog.show = true;
        },
        async confirmDialog() {
            let carLines = _.filter(this.insertDialog.lines, r => r.status === -2);
            carLines = _.filter(
                this.insertDialog.remoteLines,
                p => _.findIndex(carLines, l => l.car.vId === p.car.vId) > -1,
            );
            const uid = await this.resourceStore.onGenerateReceipt(
                carLines,
                this.insertDialog.business.getOption('receiptType', ''),
            );
            this.insertDialog.show = false;
            this.$router.push(`/receipt-list/${uid}`);
        },
        handleDeleteReceipt(item) {
            this.receiptManager
                .deleteReceipt(item)
                .then(() => {
                    this.$message({
                        type: 'success',
                        message: '删除成功！',
                    });
                })
                .catch(error => {
                    this.$message({
                        type: 'error',
                        message: `删除失败，${error}`,
                    });
                });
        },
        onSelect(item) {
            this.receiptManager.loadOneReceipt(item);
        },
        goDetail(item) {
            this.$router.push(`/receipt-list/${item.id}`);
        },
        reset() {
            this.init();
            this.$refs.searchInput.text = '';
        },
    },
};
</script>

<style lang="scss" scoped>
.document-list {
    display: flex;
    flex-flow: column nowrap;

    .document-list-top {
        padding: 0 10px;
    }

    .document-list-main {
        flex: 1;
        overflow-x: hidden;
        overflow-y: auto;
        padding: 0 10px;

        .no-document {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            font-size: 24px;
            color: #bbb;
        }
    }
}

.search-input {
    width: 360px;
}
</style>



