<template>
    <div class="flex-row">
        <el-upload v-if="carInfoMode || receiptModel" class="upload-box upload-demo"
            ref="upload" :action="fileUrl" :http-request="upload" :data="uploadData"
            :headers="headers" :on-success="handleSuccess" :show-file-list="false">
            <el-button size="mini" type="primary" @click="createResource">导入车辆</el-button>
            <!-- <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div> -->
        </el-upload>
        <el-button v-if="!carInfoMode" size="mini" type="primary" @click="showInsertDialog">添加车辆</el-button>
        <el-dialog :title="carInfoMode ? '导入车辆信息' : '添加车辆'" :visible.sync="dialogVisible"
            width="80%" v-if="dialogVisible">
            <div class="note-content">
                <div class="top-button-left">
                    <template v-if="!carInfoMode && !dialogBatchAction">
                        <search-input :types="['vId','eId','note']"
                            :expectedTypes="['vId','vIds']" class="btn-box search-input"
                            @select="onSelectSearchInfo" ref="searchInput" />
                    </template>
                </div>
            </div>
            <div class="note-content">
                <div class="top-button-left">
                    <template v-if="!dialogBatchAction && carsInfoListfromExcel.length">
                        <div class="btn-box">
                            <el-button :size="dialogBtnSize" type="primary"
                                @click="onDialogBatchAction">批量操作</el-button>
                        </div>
                    </template>
                    <template v-if="dialogBatchAction">
                        <div class="btn-box">
                            <el-button :size="dialogBtnSize" type="danger"
                                @click="onDialogBatchDelete">删除</el-button>
                        </div>
                        <div class="btn-box">
                            <el-button :size="dialogBtnSize" type="primary"
                                @click="onCancelDialogBatchAction">取消</el-button>
                        </div>
                    </template>
                </div>
            </div>
            <div class="content-top">
                <grid-head-row>
                    <grid-head-column v-if="dialogBatchAction" flexWidth="0 0 30px"
                        textAlign="center">
                        <el-checkbox v-model="isDialogAllChecked"
                            :indeterminate="isDialogHasChecked && !isDialogAllChecked" />
                    </grid-head-column>
                    <grid-head-column v-for="field in manager.listFields" v-if="!business.isHiddenField(field.name)"
                        :key="field.name" :flexWidth="field.getOption('contentWidth', '0 0 auto')"
                        :textAlign="field.getOption('contentAlign', 'center')">
                        {{field.label}}
                    </grid-head-column>
                    <grid-head-column v-if="carInfoMode" flexWidth="0 0 90px"
                        textAlign="center">
                        提示
                    </grid-head-column>
                    <grid-head-column flexWidth="0 0 80px" textAlign="right" />
                </grid-head-row>
            </div>
            <div class="content">
                <template v-for="item in carsInfoListfromExcel">
                    <cars-info-list-item :fields="manager.listFields" :key="item.car.vId"
                        :item="item" :batch-action="dialogBatchAction"
                        :business="business" mode="dialog" @carInfoSelect="carInfoSelect"
                        @goCarInfoDetail="goCarInfoDetail" @deleteCarInfo="deleteCarInfo" />
                </template>
                <p v-if="carsInfoListfromExcel.length === 0" class="content-p">
                    {{ carInfoMode ? '请导入数据' : '请搜索以添加数据' }}
                </p>
            </div>

            <span slot="footer" class="dialog-footer">
                <el-button size="mini" @click="cancelDialog">取 消</el-button>
                <el-button type="primary" size="mini" @click="showDialogTwo">确定</el-button>
            </span>
        </el-dialog>

        <el-dialog title="备注信息" :visible.sync="dialogTwo" width="30%" v-if="dialogTwo">
            <div class="smmary">
                <pt-title size="14" type="text" style="width: 50px;">备注:</pt-title>
                <el-input v-model="manager.remark" size="mini" style="width: 250px;" />
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button size="mini" @click="closeDialogTwo">取 消</el-button>
                <el-button type="primary" size="mini" @click="closeInsertDialog">确定</el-button>
            </span>
        </el-dialog>

    </div>
</template>

<script>
import putUpload from '../../utils/put-upload';
import ResourceUtil from '../../api/module-resource/resource-util';

export default {
    name: 'cars-insert-excel-dialog-list',
    props: {
        manager: { type: Object },
    },
    mounted() {},
    data() {
        return {
            fileUrl: '',
            dialogVisible: false,
            dialogTwo: false,
            note: '',
            uploadData: {
                dataName: 'excel',
            },
            excelUid: '',
            operationToken: '',
            mode: { type: String },
        };
    },
    computed: {
        business() {
            return this.manager.listBusiness;
        },
        fields() {
            return this.manager.listFields;
        },
        carsInfoListfromExcel: {
            get() {
                return this.manager.carsInfoListfromExcel
                    ? this.manager.carsInfoListfromExcel
                    : this.manager.newCarsInfoList;
            },
            set(value) {
                if (this.manager.carsInfoListfromExcel) {
                    this.manager.carsInfoListfromExcel = value;
                } else if (this.manager.newCarsInfoList) {
                    this.manager.newCarsInfoList = value;
                }
            },
        },
        headers() {
            return {
                operationToken: this.operationToken,
                'Ocp-Apim-Subscription-Key': 'ad91c2f95eab4af7aa1efe4adb9a6e60',
            };
        },
        isDialogAllChecked: {
            get() {
                return this.manager.isDialogAllChecked;
            },
            set(value) {
                this.manager.isDialogAllChecked = value;
            },
        },
        isDialogHasChecked: {
            get() {
                return this.manager.isDialogHasChecked;
            },
        },
        dialogBatchAction: {
            get() {
                return this.manager.dialogBatchAction;
            },
        },
        carInfoMode: {
            get() {
                return this.$route.name === 'CarsInfoList';
            },
        },
        receiptModel: {
            get() {
                return this.$route.name === 'ReceiptDetail';
            },
        },
        dialogBtnSize: {
            get() {
                return this.carInfoMode ? 'mini' : 'small';
            },
        },
    },
    methods: {
        createResource() {
            this.manager.onShowDetail(false);
            this.manager.onCancelDialogBatchAction();
            if (this.manager.carsInfoListfromExcel) {
                this.loadResource().then(result => {
                    this.operationToken = result.operationToken;
                    this.excelUid = result.uid;
                    this.fileUrl = `${serverApiConfig.resource}/v1/${this.excelUid}`;
                });
            }
        },
        loadResource() {
            return ResourceUtil.createOneResource('');
        },
        handleSuccess() {
            this.manager.loadExcelCarsList(this.excelUid);
            this.dialogVisible = true;
        },
        upload(item) {
            return putUpload(item);
        },
        showInsertDialog() {
            this.carsInfoListfromExcel = [];
            this.dialogVisible = true;
        },
        closeInsertDialog() {
            if (this.carsInfoListfromExcel.length === 0) {
                return;
            }

            let saveFn;

            if (this.manager.saveCarsForExcel) {
                saveFn = this.manager.saveCarsForExcel;
            } else {
                saveFn = this.manager.InsertCarInfosToList;
            }

            saveFn()
                .then(() => {
                    this.carsInfoListfromExcel = [];

                    if (this.manager.saveCarsForExcel) {
                        this.manager.loadCarsInfoList();
                        this.manager.remark = '';
                    }
                    this.dialogVisible = false;
                    this.dialogTwo = false;

                    if (this.manager.saveCarsForExcel) {
                        this.$message({
                            type: 'success',
                            message: '添加成功！',
                        });
                    } else {
                        this.$message({
                            type: 'success',
                            message: '已添加可添加项！',
                        });
                    }
                })
                .catch(() => {
                    this.$message({
                        type: 'error',
                        message: '添加失败！',
                    });
                });
        },
        cancelDialog() {
            this.dialogVisible = false;
            this.carsInfoListfromExcel = [];
        },
        showDialogTwo() {
            if (this.carsInfoListfromExcel.length === 0) {
                this.$message({
                    type: 'error',
                    message: '无数据保存！',
                });
                this.dialogVisible = false;
                return;
            }
            if (!this.manager.saveCarsForExcel) {
                this.closeInsertDialog();

                return;
            }
            this.dialogTwo = true;
        },
        closeDialogTwo() {
            this.dialogTwo = false;
        },
        carInfoSelect(item) {
            this.$emit('carInfoSelect', item);
        },
        goCarInfoDetail(item) {
            this.$emit('goCarInfoDetail', item);
        },
        deleteCarInfo(item) {
            this.$emit('deleteCarInfo', item);
        },
        onDialogBatchAction() {
            this.$emit('onDialogBatchAction');
        },
        onCancelDialogBatchAction() {
            this.$emit('onCancelDialogBatchAction');
        },
        onDialogBatchDelete() {
            this.$emit('onDialogBatchDelete');
        },
        onSelectSearchInfo(item) {
            this.manager.onSelectSearchInfo(item);
        },
    },
};
</script>

<style lang="scss" scoped>
.cars-insert-excel {
    .cars-inser-excel-top {
        display: flex;
    }
    .top-button-left {
        display: flex;
        align-items: center;
        width: 300px;
        margin-bottom: 10px;

        .btn-box {
            margin: 0 10px 0 0;
        }
    }
    .note-content {
        display: flex;
        width: 300px;
    }
    .content-top {
        margin-top: 10px;
    }
    .content {
        height: 300px;
        overflow-y: auto;
    }
    .content-p {
        margin-top: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #ccc;
        font-size: 24px;
    }
    .upload-box {
        margin-right: 10px;
        margin-left: 10px;
    }
    .smmary {
        display: flex;
    }

    .search-input {
        width: 360px;
    }
}
</style>

<style lang="scss">
.upload-box {
    .el-button {
        height: 40px;
    }
}
</style>



