<template>
    <div :class="fileListItemClass" @click="fileSelect">
        <grid-cell v-if="batchAction" flexWidth="0 0 30px" justify-content="center">
            <template name="view">
                <div @click.stop>
                    <el-checkbox v-model="item.isChecked" />
                </div>
            </template>
        </grid-cell>
        <grid-cell flexWidth="0 0 500px" justify-content="left">
            <template name="view">
                <pt-title size="14" type="text" important>{{item.fileName}}</pt-title>
            </template>
        </grid-cell>
        <grid-cell flexWidth="0 0 180px" justify-content="left">
            <template name="view">
                <div @click="fileToCar">
                    <pt-title size="14" type="text" :class="vidTitle" important>{{item.vId}}</pt-title>
                </div>
            </template>
        </grid-cell>
        <grid-cell flexWidth="0 0 30px" justify-content="center" v-if="mode === 'carInfoFiles'">
            <template name="view" v-if="!suffix">
                <el-tooltip content="未匹配" placement="top" effect="light">
                    <i class="el-icon-error" style="color: #f56c6c;"></i>
                </el-tooltip>
            </template>
            <template name="view" v-if="suffix">
                <el-tooltip content="匹配成功" placement="top" effect="light">
                    <i class="el-icon-success" style="color: #67c23a;"></i>
                </el-tooltip>
            </template>
        </grid-cell>
        <grid-cell flexWidth="0 0 60px" justify-content="flex-end">
            <template v-if="item.isSelect">
                <template name="view">
                    <el-button type="danger" icon="el-icon-delete" size="mini"
                        class="icon-btn" @click="deleteFile" />
                </template>
            </template>
        </grid-cell>
    </div>
</template>

<script>
export default {
    name: 'file-list-item',
    props: {
        item: { type: Object },
        mode: { type: String },
        batchAction: { type: Boolean, default: false },
        fetchSuffixIcon: { type: Function, required: true },
    },
    data() {
        return {};
    },
    computed: {
        fileListItemClass: {
            get() {
                return {
                    'file-list-item': true,
                    'file-list-item-select': this.item.isSelect,
                };
            },
        },
        vidTitle() {
            return {
                vidTitle: this.item.fileCheck,
            };
        },
        suffix() {
            if (this.fetchSuffixIcon) {
                // eslint-disable-next-line
                this.item.fileCheck = this.fetchSuffixIcon(this.item);
            }
            return this.item.fileCheck;
        },
    },
    methods: {
        fileToCar() {
            if (this.item.fileCheck) {
                this.$emit('fileToCar', this.item.vId);
            }
        },
        deleteFile() {
            this.$emit('deleteFile', this.item);
        },
        fileSelect() {
            this.$emit('fileSelect', this.item);
        },
    },
};
</script>

<style lang="scss" scoped>
.file-list-item {
    display: flex;
    align-items: center;
    min-height: 40px;
    padding-left: 5px;
    cursor: pointer;

    &:nth-child(2n + 1) {
        background-color: #faf5ed;
    }

    &:nth-child(2n) {
        background-color: #ffe5bb;
    }

    &:hover {
        background-color: #c0ccda;
    }

    .icon-btn {
        width: 30px;
        padding: 5px 5px;
    }
}
.vidTitle {
    text-decoration: underline;
}
.file-list-item-select {
    background-color: #9ca8b6 !important;
}
</style>



