<template>
    <el-dialog :title="title" :visible.sync="isDialogShow" width="90%">
        <pt-layout vertical-mode fixedBlockBasis="0 0 50px">
            <div slot="first" class="flex-row search-line">
                <div class="col button-list">
                    <template v-if="selectCar && !dialogBatchAction">
                        <search-input :types="['vId','eId','note','receipt']"
                            :expectedTypes="['vId','vIds']" class="btn-box search-input"
                            @select="onSelectSearchInfo" ref="searchInput" />
                    </template>
                    <template v-if="!dialogBatchAction && lines.length">
                        <div class="btn-box">
                            <el-button :size="dialogBtnSize" type="primary"
                                @click="onDialogBatchAction">批量操作</el-button>
                        </div>
                    </template>
                    <template v-if="dialogBatchAction">
                        <div class="btn-box">
                            <el-button :size="dialogBtnSize" type="danger"
                                @click="onDialogBatchDelete">删除</el-button>
                        </div>
                        <div class="btn-box">
                            <el-button :size="dialogBtnSize" type="primary"
                                @click="onCancelDialogBatchAction">取消</el-button>
                        </div>
                    </template>
                </div>
            </div>
            <pt-layout slot="second" :grow-block-scroll="true" vertical-mode
                fixedBlockBasis="none" class="car-info-list-container">
                <grid-head-row slot="first">
                    <grid-head-column v-if="dialogBatchAction" flexWidth="0 0 30px"
                        textAlign="center">
                        <el-checkbox v-model="isDialogAllChecked"
                            :indeterminate="isDialogHasChecked && !isDialogAllChecked" />
                    </grid-head-column>
                    <grid-head-column v-for="field in fields" v-if="!business.isHiddenField(field.name)"
                        :key="field.name" :flexWidth="field.getOption('contentWidth', '0 0 auto')"
                        :textAlign="field.getOption('contentAlign', 'center')">
                        {{ field.label }}
                    </grid-head-column>
                    <grid-head-column v-if="showStatus" flexWidth="0 0 90px"
                        textAlign="center">
                        提示
                    </grid-head-column>
                    <grid-head-column flexWidth="0 0 80px" textAlign="right" />
                </grid-head-row>
                <div slot="second" class="content-list">
                    <template v-for="item in lines">
                        <cars-info-list-item :fields="fields" :key="item.car.vId"
                            :item="item" :batch-action="dialogBatchAction"
                            :business="business" mode="dialog" @carInfoSelect="onCarInfoSelect(item)">
                            <template slot="line-suffix">
                                <grid-cell v-if="showStatus" flexWidth="0 0 90px"
                                    justify-content="center">
                                    <template name="view">
                                        <pt-title size="14" type="text"
                                            important :style="{'color': stateColor(item)}">{{item.statusText}}</pt-title>
                                    </template>
                                </grid-cell>
                                <grid-cell v-if="item.isSelect" flexWidth="0 0 40px"
                                    justify-content="flex-end">
                                    <template name="view">
                                        <el-button icon="el-icon-delete" type="danger"
                                            class="icon-btn" @click="onDeleteCarInfo(item)" />
                                    </template>
                                </grid-cell>
                            </template>
                        </cars-info-list-item>
                    </template>
                    <p v-if="lines.length === 0" class="content-p">
                        {{ messageContent}}
                    </p>
                </div>
            </pt-layout>
        </pt-layout>
        <span slot="footer" class="dialog-footer">
            <el-button size="mini" @click="closeDialog(false)">取 消</el-button>
            <el-button type="primary" size="mini" @click="closeDialog(true)">确定</el-button>
        </span>
    </el-dialog>
</template>

<script>
import _ from 'lodash';
import dialog from '../../components/mixins/dialog';

export default {
    name: 'CarListDialog',
    mixins: [dialog],
    data() {
        return {
            dialogBatchAction: false,
        };
    },
    props: {
        business: { type: Object, required: true },
        fields: { type: Array, required: true },
        lines: { type: Array, required: true },
        showStatus: { type: Boolean, default: false },
        mode: { type: String },
    },
    methods: {
        onEvent(eventName, line) {
            this.$emit(eventName, line);
        },
        onDialogBatchAction() {
            this.dialogBatchAction = true;
        },
        onCancelDialogBatchAction() {
            this.dialogBatchAction = false;

            this.clearChecked();
        },
        onDialogBatchDelete() {
            this.$confirm('是否确认删除选中项？', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
            })
                .then(() => {
                    this.removeDeletedCarInfo(this.fetchCheckedCarInfo())
                        .then(() => {
                            this.$message({
                                type: 'success',
                                message: '删除成功！',
                            });

                            this.emptyListCancelBatchAction();
                        })
                        .catch(() => {
                            this.$message({
                                type: 'error',
                                message: '删除失败！',
                            });
                        });
                })
                .catch(() => {});
        },
        onSelectSearchInfo(item) {
            this.$emit('onSelectSearchInfo', item);
        },
        clearChecked() {
            _.each(this.lines, carInfo => {
                carInfo.isChecked = false;
            });
        },
        onCarInfoSelect(item) {
            _.each(this.lines, carInfo => {
                carInfo.isSelect = false;
            });

            item.isSelect = true;
        },
        onDeleteCarInfo(item) {
            this.$confirm('是否确认删除？', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
            })
                .then(() => {
                    this.removeDeletedCarInfo([item])
                        .then(() => {
                            this.$message({
                                type: 'success',
                                message: '删除成功！',
                            });

                            this.emptyListCancelBatchAction();
                        })
                        .catch(() => {
                            this.$message({
                                type: 'error',
                                message: '删除失败！',
                            });
                        });
                })
                .catch(() => {});
        },
        async removeDeletedCarInfo(cars) {
            _.each(cars, car => this.lines.splice(this.lines.indexOf(car), 1));

            return cars;
        },
        emptyListCancelBatchAction() {
            const list = this.lines;

            if (!list.length) {
                this.onCancelDialogBatchAction();
            }
        },
        fetchCheckedCarInfo() {
            return _.filter(this.lines, carInfo => carInfo.isChecked);
        },
        stateColor(item) {
            switch (item.status) {
                case -1:
                    return 'green';
                case 0:
                    return '#2c96fd';
                case 1:
                    return 'red';
                default:
                    break;
            }
            return '';
        },
    },
    computed: {
        lineStatus() {
            return 'ok';
        },
        title() {
            switch (this.mode) {
                case 'carsExcel':
                    return '导入车辆信息';
                case 'selectCar':
                    return '添加车辆';
                default:
                    return '';
            }
        },
        messageContent() {
            switch (this.mode) {
                case this.mode === this.carsExcel:
                    return '请导入数据';
                case this.mode === this.selectCar:
                    return '请搜索以添加数据';
                default:
                    return '';
            }
        },
        headers() {
            return {
                operationToken: this.operationToken,
                'Ocp-Apim-Subscription-Key': 'ad91c2f95eab4af7aa1efe4adb9a6e60',
            };
        },
        carsExcel: {
            get() {
                return this.mode === 'carsExcel';
            },
        },
        selectCar: {
            get() {
                return this.mode === 'selectCar';
            },
        },
        dialogBtnSize: {
            get() {
                return this.carsExcel ? 'mini' : 'small';
            },
        },
        isDialogAllChecked: {
            get() {
                return this.lines.length && !_.some(this.lines, carinfo => !carinfo.isChecked);
            },
            set(value) {
                _.each(this.lines, carinfo => {
                    carinfo.isChecked = value;
                });
            },
        },
        isDialogHasChecked: {
            get() {
                return _.some(this.lines, carinfo => carinfo.isChecked);
            },
        },
    },
};
</script>

<style lang="scss" scoped>
.icon-btn {
    width: 30px;
    padding: 5px 5px;
}

.search-line {
    margin-bottom: 10px;
}

.content-list {
    height: 400px;
    min-height: 200px;
}

.search-input {
    width: 360px;
}

.content-p {
    margin-top: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #ccc;
    font-size: 24px;
}

.car-info-list-container {
    // height: 40vh;
    min-height: 240px;
}

.btn-box {
    margin-left: 5px;

    &:first-child {
        margin-left: 0;
    }
}
</style>



