<template>
    <pt-layout verticalMode fixedBlockBasis="50px"  class="car-info-detail-container">
        <div slot="first" class="flex-row">
            <div class="col grow">
                <pt-layout fixed-block="second">
                    <template slot="first">
                        <template v-if="!isEdit">
                            <el-button v-if="editable" type="primary" @click="onDetailEdit">编辑</el-button>
                            <el-button v-if="deletable" type="danger" @click="deleteCarInfo">
                                删除
                            </el-button>
                        </template>
                        <template v-if="isEdit">
                            <el-button type="default" @click="onDetailCancel">取消</el-button>
                            <el-button type="primary" @click="onDetailSave">保存</el-button>
                        </template>
                    </template>
                    <template slot="second">
                        <template v-if="!isEdit">
                            <div class="close-detail-icon" @click="onShowDetail(false)">
                                <i class="el-icon-close" />
                            </div>
                        </template>
                    </template>
                </pt-layout>
            </div>
        </div>
        <div slot="second" class="car-info-detail">
            <div
                v-for="(group, key) in groups"
                v-if="isGroupHasNotHiddenField(group)"
                :key="key"
                class="key-value-container"
            >
                <div class="group">
                    <div class="group-name">
                        <pt-title important type="text">
                            {{ key }}:
                        </pt-title>
                    </div>
                    <div class="separation"></div>
                </div>
                <div class="key-value-box">
                    <car-info-detail-key-value v-for="field in group"
                        v-if="!business.isHiddenField(field.name, carInfo.car)" :key="field.name"
                        :car-info="carInfo.car" :field="field"
                        :isEdit="isEdit && !business.isDisableField(field.name, carInfo.car)"
                        :validator="validator[field.name]" @valueClick="onValueClick" />
                </div>
            </div>
        </div>
    </pt-layout>
</template>

<script>
import _ from 'lodash';

export default {
    name: 'CarInfoDetail',
    props: {
        carInfo: { type: Object, required: true },
        business: { type: Object, required: true },
        validator: { type: Object, required: true },
        editable: { type: Boolean, required: false, default: false },
        isEdit: { type: Boolean, required: false, default: false },
        deletable: { type: Boolean, required: false, default: false },
    },
    computed: {
        groups: {
            get() {
                return this.business.getGroupFieldsByOption(
                    'groupName',
                    '其它',
                );
            },
        },
    },
    methods: {
        onValueClick(item, field) {
            if (field.fieldType === 'CSV') {
                this.$router.push(
                    `/receipt-list/${_.last(
                        item.getValue(field.name).split(','),
                    )}`,
                );
            }
        },
        isGroupHasNotHiddenField(group) {
            return _.some(
                group,
                field =>
                    !this.business.isHiddenField(field.name, this.carInfo.car),
            );
        },
        onDetailEdit() {
            this.$emit('onDetailEdit');
        },
        onDetailCancel() {
            this.$emit('onDetailCancel');
        },
        onDetailSave() {
            this.$emit('onDetailSave');
        },
        onShowDetail(visible) {
            this.$emit('onShowDetail', visible);
        },
        deleteCarInfo() {
            this.$emit('deleteCarInfo', this.carInfo);
        },
    },
};
</script>

<style lang="scss" scoped>
.car-info-detail-container {
    padding-left: 10px;
    border: 1px solid #bbb;
    border-bottom: none;
}

.car-info-detail {
    display: flex;
    flex-flow: column nowrap;
    overflow: auto;

    .title {
        position: relative;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        flex: none;
        height: 40px;

        .title-bond {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
        }
    }

    .key-value-container {
        flex: none;
        display: flex;
        flex-flow: column nowrap;
        padding: 20px 0 0;

        .group {
            display: flex;
            align-items: center;
            margin-bottom: 20px;

            .group-name {
                flex: none;
            }

            .separation {
                flex: auto;
                border-bottom: 1px solid #bbb;
                margin: 2px 10px 0 5px;
            }
        }

        .key-value-box {
            flex: none;
            display: flex;
            flex-flow: row wrap;
        }
    }
}
</style>

<style lang="scss">
.close-detail-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    font-size: 16px;
    cursor: pointer;
}

.car-info-detail {
    .title {
        max-width: 100%;
        overflow: hidden;

        .pt-title {
            max-width: 100%;
            overflow: hidden;

            .wrapper {
                max-width: 100%;
                overflow: hidden;

                span {
                    max-width: 100%;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
            }
        }
    }
}
</style>



