import _ from 'lodash';
import CarLine from '../models/car-line';
import CarInfoConfig from '../config/car-info-config';
import CarInfoApi from '../../api/module-car-info/car-info-api';
import ResourceStore from '../../module-receipt/js/resource-store';
import Configuration from '../config/configuration';

export default class CarsInfoListManager {
    constructor() {
        this.resourceStore = new ResourceStore();
        this._listBusiness = Configuration.CarInfo;
        this._detailBusiness = Configuration.CarDetail;
        this._detailValidator = {};
        this._listFields = this._listBusiness.getOrderedFieldsByOption('listOrders', Infinity);

        this.mode = 'list';
        this.carsInfoList = [];

        this.carsInfoListfromExcel = [];
        this.operationToken = '';
        this.newUid = '';

        this.isShowDetail = false;
        this.isDetailEdit = false;
        this.detailCarLine = new CarLine(this._detailBusiness.createCar({}), {});

        this.batchAction = false;
        this.dialogBatchAction = false;

        this.isNewDetailCarLine = false;
    }

    get listFields() {
        return this._listFields;
    }

    get listBusiness() {
        return this._listBusiness;
    }

    get detailBusiness() {
        return this._detailBusiness;
    }

    get detailValidator() {
        return this._detailValidator;
    }

    get isAllChecked() {
        return this.carsInfoList.length && !_.some(this.carsInfoList, carinfo => !carinfo.isChecked);
    }
    set isAllChecked(value) {
        _.each(this.carsInfoList, carinfo => {
            carinfo.isChecked = value;
        });
    }

    get isHasChecked() {
        return _.some(this.carsInfoList, carinfo => carinfo.isChecked);
    }

    get isDialogAllChecked() {
        return this.carsInfoListfromExcel.length && !_.some(this.carsInfoListfromExcel, carinfo => !carinfo.isChecked);
    }
    set isDialogAllChecked(value) {
        _.each(this.carsInfoListfromExcel, carinfo => {
            carinfo.isChecked = value;
        });
    }

    get isDialogHasChecked() {
        return _.some(this.carsInfoListfromExcel, carinfo => carinfo.isChecked);
    }

    async loadCarsInfoList() {
        const result = await CarInfoApi.fetchCars(['']);
        const arrs = [];

        _.each(result, r => {
            arrs.push(this.carsFactory(r));
        });

        arrs.sort((a, b) => {
            if (a.vId < b.vId) {
                return -1;
            } else if (a.vId > b.vId) {
                return 1;
            }
            return 0;
        });

        this.onShowDetail(false);
        this.carsInfoList = arrs;
    }

    async selectCarSInfoList(item) {
        const arrs = [];
        if (_.isArray(item.result)) {
            const result = await CarInfoApi.fetchCars(item.result);
            _.each(result, r => {
                arrs.push(this.carsFactory(r));
            });
        } else {
            const result = await CarInfoApi.fetchCars([item.result]);
            _.each(result, r => {
                arrs.push(this.carsFactory(r));
            });
        }

        this.onShowDetail(false);
        this.carsInfoList = arrs;
    }

    carsFactory(obj) {
        return new CarLine(this._listBusiness.createCar(obj), {});
    }

    transformOriginalCarInfo = info =>
        _.reduce(
            CarInfoConfig.carInfoConfig,
            (r, value) => {
                const newResult = r;

                if (info[value.excelKey]) {
                    if (value.resolveInitialValue) {
                        newResult[value.key] = value.resolveInitialValue(info[value.excelKey].value);
                    } else {
                        newResult[value.key] = info[value.excelKey].value;
                    }
                } else {
                    newResult[value.key] = '';
                }

                return newResult;
            },
            {},
        );

    async getCarsFromExcelAndCompare(carObjsFromExcel) {
        const carObjIds = _.map(carObjsFromExcel, item => item.car.vId);

        const fetchCarsResult = await CarInfoApi.fetchCars(carObjIds);

        const carsListFromResource = [];
        _.each(fetchCarsResult, r => {
            carsListFromResource.push(this.carsFactory(r));
        });

        this.compareCarsListForExcelAndResorce(carObjsFromExcel, carsListFromResource);
    }

    compareCarsListForExcelAndResorce(carObjsFromExcel, carsListFromResource) {
        if (carObjsFromExcel.length === 0) {
            return;
        }
        _.each(carObjsFromExcel, r => {
            const index = _.findIndex(carsListFromResource, k => k.car.vId === r.car.vId);
            if (index === -1) {
                r.status = -1;
            } else if (index > -1) {
                r.status = this.compareCarModel(r.car, carsListFromResource[index].car);
            }
        });
    }

    compareCarModel(excelObj, sourceObj) {
        let flag = 0;
        const excelObjKeys = _.map(excelObj.toData(), r => r.key);
        _.each(excelObjKeys, r => {
            if (excelObj[r] !== sourceObj[r]) {
                flag = 1;
            }
        });

        return flag;
    }

    saveCarsForExcel = async (lines, remark) => this.saveCars(lines, remark);

    onShowDetail = visible => {
        if (this.batchAction && visible) {
            return;
        }

        if (!visible) {
            this.onDetailCancel();
        }

        this.isShowDetail = visible;
    };

    onDetailEdit = async () => {
        this.detailCarLine.onEdit();
        this.isDetailEdit = true;
    };

    onDetailCancel = async () => {
        this.detailCarLine.onEditCancel();
        this.isDetailEdit = false;
        this.isNewDetailCarLine = false;
    };

    onDetailSave = async () => {
        this.detailCarLine.onEditConfirm();

        await this.saveCars([this.detailCarLine]);

        if (!this.carsInfoList.includes(this.detailCarLine)) {
            this.carsInfoList.unshift(this.detailCarLine);
        }

        this.isDetailEdit = false;
    };

    goDetail(item) {
        this.isDetailEdit = false;

        if (item) {
            this.isNewDetailCarLine = false;
            this.detailCarLine = item;
        } else {
            this.isNewDetailCarLine = true;
            this.detailCarLine = new CarLine(this._detailBusiness.createCar({}), {});
        }

        this._detailValidator = this._detailBusiness.createValidator(this.detailCarLine.car);
    }

    async queryCarsForSearching(queryString) {
        const keyword = queryString;
        const types = 'vId,eId,note';
        const returnType = 'vIds';
        return CarInfoApi.getCarsAndReceiptList(keyword, returnType, types).then(res =>
            _.map(res, r => this.carsFactory(r)),
        );
    }

    onCarInfoSelect = (item, type) => {
        const list = this.fetchListByType(type);

        _.each(list, carInfo => {
            carInfo.isSelect = false;
        });

        item.isSelect = true;
    };

    deleteCarInfo = async (items, type) => {
        if (!type) {
            await this.deleteCars(items);
        }

        return this.removeDeletedCarInfo(items, type);
    };

    removeDeletedCarInfo = (cars, type) => {
        const list = this.fetchListByType(type);

        _.each(cars, car => list.splice(list.indexOf(car), 1));
    };

    fetchCheckedCarInfo = type => {
        const list = this.fetchListByType(type);

        return _.filter(list, carInfo => carInfo.isChecked);
    };

    fetchCheckedNotOriginalStatusCarInfo = type => {
        const list = this.fetchListByType(type);

        return _.filter(list, carInfo => carInfo.isChecked && `${carInfo.car.status}` === '0');
    };

    onBatchAction = () => {
        this.onShowDetail(false);
        this.batchAction = true;
    };

    onCancelBatchAction = () => {
        this.batchAction = false;

        this.clearChecked();
    };

    onDialogBatchAction = () => {
        this.dialogBatchAction = true;
    };

    onCancelDialogBatchAction = () => {
        this.dialogBatchAction = false;

        this.clearChecked('excel');
    };

    clearChecked = type => {
        const list = this.fetchListByType(type);

        _.each(list, carInfo => {
            carInfo.isChecked = false;
        });
    };

    fetchListByType = type => {
        if (type === 'excel') {
            return this.carsInfoListfromExcel;
        }

        return this.carsInfoList;
    };

    modifyCarsToUsed = async (cars, used) => {
        await Promise.all(
            _.map(cars, async carInfo => {
                carInfo.used = used;
            }),
        );

        return true;
    };

    onGenerateReceipt = async (cars, type) => this.resourceStore.onGenerateReceipt(cars, type);

    checkedItemCanShouldGenerateReceipt(type) {
        const list = this.fetchCheckedCarInfo();
        return this.resourceStore.checkedItemCanShouldGenerateReceipt(type, list);
    }

    isCheckedItemHasShouldInbound = () => {
        const list = this.fetchCheckedCarInfo();

        return _.some(list, carInfo => (carInfo.car.status & 1) !== 1);
    };

    isCheckedItemAllShouldInbound = () => {
        const list = this.fetchCheckedCarInfo();

        return !_.some(list, carInfo => (carInfo.car.status & 1) === 1);
    };

    isCheckedItemHasShouldOutbound = () => {
        const list = this.fetchCheckedCarInfo();

        return _.some(list, carInfo => (carInfo.car.status & 16) !== 16 && (carInfo.car.status & 1) === 1);
    };

    isCheckedItemHasOrginalStatus = () => {
        const list = this.fetchCheckedCarInfo();

        return _.some(list, carInfo => `${carInfo.car.status}` === '0');
    };

    // Api

    saveCars = (cars, remark) => {
        const carsInfo = _.map(cars, carInfo => carInfo.car.toData());

        return CarInfoApi.saveCars(carsInfo, remark);
    };

    deleteCars = cars => {
        const carIdArray = _.map(cars, carInfo => carInfo.car.vId);

        return CarInfoApi.deleteCars(carIdArray.join(','));
    };
}



