import _ from 'lodash';
import Business from './business';
import BusinessFieldDecortor from './business-field-decorator';
import CompositeField from './composite-field';
import Field from './field';

const CAR_INFO = 'BUSINESS_CAR_INFO';
const CAR_INFO_DETAIL = 'BUSINESS_CAR_INFO_DETAIL';
const CAR_RECEIPT_INBOUND = 'BUSINESS_CAR_RECEIPT_INBOUND';
const CAR_RECEIPT_OUTBOUND = 'BUSINESS_CAR_RECEIPT_OUTBOUND';
const CAR_RECEIPT_INBOUND_CUSTOMS_DECLARATION = 'BUSINESS_RECEIPT_INBOUND_CUSTOMS_DECLARATION';
const CAR_RECEIPT_INBOUND_CERTIFICATE = 'BUSINESS_RECEIPT_CERTIFICATE';
const GROUP_NAME_INDENTY = '车辆识别信息';
const GROUP_NAME_BASIC = '车辆基础信息';
const GROUP_NAME_SUFACE = '车辆外观信息';
const GROUP_NAME_SPECIFICATION = '车辆规格信息';
const GROUP_NAME_STATUS = '车辆状态';

const fieldDefinition = {
    vId: new Field(
        'vId',
        '底盘号',
        '底盘号',
        String,
        [
            CAR_INFO,
            CAR_INFO_DETAIL,
            CAR_RECEIPT_INBOUND,
            CAR_RECEIPT_OUTBOUND,
            CAR_RECEIPT_INBOUND_CUSTOMS_DECLARATION,
            CAR_RECEIPT_INBOUND_CERTIFICATE,
        ],
        {
            contentWidth: '0 0 170px',
            contentAlign: 'left',
            groupName: GROUP_NAME_INDENTY,
            required: true,
            maxLength: 20,
            minLength: 15,
            regexp: /^[A-Za-z0-9]+$/,
            regexErrorMessage: '只能是数字或文本',
            readonlyStatus: ['used'],
        },
    ),
    eId: new Field(
        'eId',
        '发动机号',
        '发动机号',
        String,
        [
            CAR_INFO,
            CAR_INFO_DETAIL,
            CAR_RECEIPT_INBOUND,
            CAR_RECEIPT_OUTBOUND,
            CAR_RECEIPT_INBOUND_CUSTOMS_DECLARATION,
            CAR_RECEIPT_INBOUND_CERTIFICATE,
        ],
        {
            contentWidth: '0 0 120px',
            contentAlign: 'left',
            groupName: GROUP_NAME_INDENTY,
            required: true,
        },
    ),
    model: new Field(
        'model',
        'Model',
        'Model',
        String,
        [
            CAR_INFO,
            CAR_INFO_DETAIL,
            CAR_RECEIPT_INBOUND,
            CAR_RECEIPT_OUTBOUND,
            CAR_RECEIPT_INBOUND_CUSTOMS_DECLARATION,
            CAR_RECEIPT_INBOUND_CERTIFICATE,
        ],
        {
            contentWidth: '0 0 140px',
            groupName: GROUP_NAME_SPECIFICATION,
        },
    ),
    brand: new Field('brand', '品牌', '品牌', String, [CAR_INFO, CAR_INFO_DETAIL], {
        contentWidth: '0 0 80px',
        groupName: GROUP_NAME_BASIC,
    }),
    specification: new Field('specification', '规格', '规格', String, [CAR_INFO], {
        contentWidth: '0 0 100px',
        groupName: GROUP_NAME_SPECIFICATION,
    }),
    color: new Field('color', '外色', '外色', String, [CAR_INFO, CAR_INFO_DETAIL], {
        contentWidth: '0 0 60px',
        groupName: GROUP_NAME_SUFACE,
    }),
    weight: new Field('weight', '重量（千克）', 'Gross Weight (Kgs)', Number, [CAR_INFO, CAR_INFO_DETAIL], {
        contentWidth: '0 0 140px',
        groupName: GROUP_NAME_SPECIFICATION,
    }),
    displacement: new Field('displacement', '排量', '排量', Number, [CAR_INFO, CAR_INFO_DETAIL], {
        contentWidth: '0 0 80px',
        groupName: GROUP_NAME_SPECIFICATION,
    }),
    productionDate: new Field('productionDate', '生产日期', '生产日期', Date, [CAR_INFO, CAR_INFO_DETAIL], {
        contentWidth: '0 0 80px',
    }),
    inbound: new Field('inbound', '入库确认单', '入库确认单', 'CSV', [CAR_INFO, CAR_INFO_DETAIL], {
        hidden: [CAR_INFO],
        disable: [CAR_INFO_DETAIL],
        groupName: GROUP_NAME_STATUS,
    }),
    outbound: new Field('outbound', '出库确认单', '出库确认单', 'CSV', [CAR_INFO, CAR_INFO_DETAIL], {
        hidden: [CAR_INFO],
        disable: [CAR_INFO_DETAIL],
        groupName: GROUP_NAME_STATUS,
    }),
    capacity: new Field('capacity', 'Capacity (c.c)', 'Capacity (c.c)', Number, [CAR_INFO, CAR_INFO_DETAIL], {
        contentWidth: '0 0 140px',
        groupName: GROUP_NAME_SPECIFICATION,
    }),
    BL: new Field('BL', 'B/L', 'B/L', String, [CAR_INFO, CAR_INFO_DETAIL], { contentWidth: '0 0 180px' }),
    inboundStatus: new Field('inbound-status', '入库确认', '入库确认', Boolean, [], {
        display: 'icon',
        icon: 'sign-in',
        trueValue: '入库确认单',
        falseValue: '未确认',
        bitValue: 1,
    }),
    outboundStatus: new Field('outbound-status', '出库确认', '出库确认', Boolean, [], {
        display: 'icon',
        icon: 'sign-out',
        trueValue: '出库确认单',
        falseValue: '未确认',
        bitValue: 16,
    }),
    inboundCustomReceipt: new Field(
        'import_declaration',
        '进境报关建议书',
        '进口报关建议书号',
        'CSV',
        [CAR_RECEIPT_INBOUND_CUSTOMS_DECLARATION, CAR_INFO_DETAIL],
        {
            contentWidth: '0 0 220px',
            contentAlign: 'left',
            editable: [CAR_RECEIPT_INBOUND_CUSTOMS_DECLARATION],
            required: true,
            suffix: true,
            groupName: GROUP_NAME_STATUS,
            disable: [CAR_INFO_DETAIL],
            hidden: [CAR_INFO],
        },
    ),
    inboundCertificateReceipt: new Field(
        'import_certificate',
        '进口证明书',
        '货物进口证明书',
        'CSV',
        [CAR_RECEIPT_INBOUND_CERTIFICATE, CAR_INFO_DETAIL],
        {
            contentWidth: '0 0 220px',
            contentAlign: 'left',
            editable: [CAR_RECEIPT_INBOUND_CERTIFICATE],
            required: true,
            suffix: true,
            groupName: GROUP_NAME_STATUS,
            disable: [CAR_INFO_DETAIL],
            hidden: [CAR_INFO],
        },
    ),
    inboundCustomStatus: new Field('inbound-custom-status', '进境报关建议书', '进口报关建议书号', Boolean, [], {
        display: 'icon',
        icon: 'file',
        trueValue: '进口报关单',
        falseValue: '未确认',
        bitValue: 4,
    }),
    inboundCertificateStatus: new Field('inbound-certificate-status', '进口证明书', '货物进口证明书', Boolean, [], {
        display: 'icon',
        icon: 'file-text',
        trueValue: '进口证明书',
        falseValue: '未确认',
        bitValue: 8,
    }),
};

const statusField = new CompositeField(
    'status',
    '状态',
    [
        CAR_INFO,
        CAR_RECEIPT_INBOUND,
        CAR_RECEIPT_OUTBOUND,
        CAR_INFO_DETAIL,
        CAR_RECEIPT_INBOUND_CUSTOMS_DECLARATION,
        CAR_RECEIPT_INBOUND_CERTIFICATE,
    ],
    {
        contentWidth: '0 0 80px',
        hidden: [
            CAR_RECEIPT_INBOUND,
            CAR_RECEIPT_OUTBOUND,
            CAR_INFO_DETAIL,
            CAR_RECEIPT_INBOUND_CUSTOMS_DECLARATION,
            CAR_RECEIPT_INBOUND_CERTIFICATE,
        ],
    },
    [fieldDefinition.inboundStatus, fieldDefinition.outboundStatus, fieldDefinition.inboundCustomStatus, fieldDefinition.inboundCertificateStatus],
);

const businessFieldDefinition = {
    vId: new BusinessFieldDecortor(fieldDefinition.vId, { listOrders: 10 }),
    eId: new BusinessFieldDecortor(fieldDefinition.eId, { listOrders: 20 }),
    model: new BusinessFieldDecortor(fieldDefinition.model, { listOrders: 2 }),
    brand: new BusinessFieldDecortor(fieldDefinition.brand, { listOrders: 30 }),
    specification: new BusinessFieldDecortor(fieldDefinition.specification, { listOrders: 50 }),
    color: new BusinessFieldDecortor(fieldDefinition.color, { listOrders: 60 }),
    weight: new BusinessFieldDecortor(fieldDefinition.weight, { listOrders: 70 }),
    BL: new BusinessFieldDecortor(fieldDefinition.BL, { listOrders: 80 }),
    displacement: new BusinessFieldDecortor(fieldDefinition.displacement, { listOrders: 90 }),
    productionDate: new BusinessFieldDecortor(fieldDefinition.productionDate, { listOrders: 100 }),
    capacity: new BusinessFieldDecortor(fieldDefinition.capacity, { listOrders: 110 }),
    statuses: new BusinessFieldDecortor(statusField, {
        listOrders: 0,
        contentAlign: 'center',
        contentWidth: '0 0 80px',
    }),
};

const selectFields = tag => {
    const businessFields = _.chain(businessFieldDefinition)
        .values()
        .filter(p => p.hasTags(tag))
        .value();
    const fields = _.chain(fieldDefinition)
        .values()
        .filter(p => p.hasTags(tag) && !_.some(businessFields, b => b.name === p.name))
        .value();
    return businessFields.concat(fields);
};

export default {
    CarInfo: new Business(CAR_INFO, selectFields(CAR_INFO)),
    CarDetail: new Business(CAR_INFO_DETAIL, selectFields(CAR_INFO_DETAIL)).doNotFormatValue(),
    Inbound: new Business(CAR_RECEIPT_INBOUND, selectFields(CAR_RECEIPT_INBOUND)),
    Outbound: new Business(CAR_RECEIPT_OUTBOUND, selectFields(CAR_RECEIPT_OUTBOUND)),
    InboundCustomsDeclaration: new Business(
        CAR_RECEIPT_INBOUND_CUSTOMS_DECLARATION,
        selectFields(CAR_RECEIPT_INBOUND_CUSTOMS_DECLARATION),
        { statusValue: 4, receiptType: 'import_declaration' },
    ),
    InboundCertificateReceipt: new Business(
        CAR_RECEIPT_INBOUND_CERTIFICATE,
        selectFields(CAR_RECEIPT_INBOUND_CERTIFICATE),
        { statusValue: 4, receiptType: 'import_certificate' },
    ),
};



