<template>
    <div class="field-value">
        <template v-if="compositeMode">
            <car-field-value v-for="childField in field.children" :key="childField.name"
                :field="childField" :item="item" :edit-mode="editMode"
                :fetch-suffix-icon="fetchSuffixIcon" @iconClick="onIconClick"
                @suffixIconClick="onSuffixIconClick" />
        </template>
        <el-tooltip v-else-if="displayIcon" :content="item.car[field.name]"
            effect="light" placement="top">
            <i :class="iconClasses" @click.stop="onIconClick(item, field)"></i>
        </el-tooltip>
        <pt-input v-else-if="editMode" v-model="value" :input-type="field.inputType"
            :validate="item.validator[field.name]" :tooltip="item.validator[field.name].message"
            size="small">
        </pt-input>
        <pt-title v-else size="14" type="text" important>
            {{ text }}
        </pt-title>
        <div v-if="suffixMode && suffixIcon.icon" :class="suffixIconClass">
            <el-tooltip :content="suffixIcon.message" effect="light" placement="top">
                <i :class="suffixIcon.icon" @click.stop="onSuffixIconClick(item, field, suffixIcon)" />
            </el-tooltip>
        </div>
    </div>
</template>

<script>
import _ from 'lodash';
import moment from 'moment';

export default {
    name: 'CarFieldValue',
    props: {
        field: { type: Object, required: true },
        item: { type: Object, required: true },
        editMode: { type: Boolean, default: false },
        fetchSuffixIcon: {
            type: Function,
            default: (field, item) => {
                if (item.car.getValue(field.name)) {
                    return {
                        icon: '',
                        iconType: 'success',
                        message: '已匹配',
                    };
                }

                return {
                    icon: '',
                    iconType: 'error',
                    message: '未匹配',
                };
            },
        },
    },
    methods: {
        onIconClick(item, field) {
            this.$emit('iconClick', item, field);
        },
        onSuffixIconClick(item, field, suffixIcon) {
            this.$emit('suffixIconClick', item, field, suffixIcon);
        },
    },
    computed: {
        carInfo() {
            return this.item.car;
        },
        csvFieldType: {
            get() {
                return this.field.fieldType === 'CSV';
            },
        },
        numberValue: {
            get() {
                return this.field.fieldType === Number;
            },
        },
        dateValue: {
            get() {
                return this.field.fieldType === Date;
            },
        },
        text: {
            get() {
                return _.isEmpty(this.value) ? '--' : this.value;
            },
        },
        value: {
            get() {
                const value = this.carInfo.getValue(this.field.name);

                if (this.csvFieldType) {
                    return _.defaultTo(_.first(value.split(',')), '');
                }

                if (this.numberValue && value && `${Number.parseFloat(value)}`) {
                    return Number.parseFloat(value).toFixed(3);
                }

                if (this.dateValue && /^\d{5}(.\d{3})?$/.test(value)) {
                    return moment('1900')
                        .add(value, 'days')
                        .format('YYYY-MM-DD');
                }

                return value;
            },
            set(value) {
                this.carInfo[this.field.name] = value;
            },
        },
        suffixMode: {
            get() {
                return this.field.getOption('suffix', false);
            },
        },
        suffixIcon: {
            get() {
                if (this.suffixMode) {
                    return this.fetchSuffixIcon(this.field, this.item);
                }

                return {
                    icon: '',
                    iconType: 'error',
                    message: '',
                };
            },
        },
        suffixIconClass: {
            get() {
                return {
                    'suffix-icon': true,
                    'suffix-icon-success': this.suffixIcon.iconType === 'success',
                    'suffix-icon-error': !this.suffixIcon.iconType || this.suffixIcon.iconType === 'error',
                };
            },
        },
        compositeMode() {
            return (
                this.field.type === 'composite' ||
                (this.field.type === 'business' && this.field.innerField.type === 'composite')
            );
        },
        icon() {
            return this.field.getOption('icon', '');
        },
        displayIcon() {
            return this.field.getOption('display', '') === 'icon';
        },
        iconClasses() {
            let iconName = this.icon;
            if (!_.isEmpty(iconName)) {
                const value = this.item.car.getValue(this.field.name);
                const classes = {
                    fa: true,
                    selected: value,
                };
                iconName = `fa-${iconName}`;
                classes[iconName] = true;
                return classes;
            }
            return [];
        },
    },
};
</script>

<style lang="scss" scoped>
.field-value {
    display: flex;
    display: -webkit-flex;
    flex-direction: row;

    i.fa {
        color: $ptit-negative;
        margin: 3px;
    }

    i.fa.selected {
        color: $ptit-positive-darker;
    }

    .suffix-icon {
        display: flex;
        justify-content: center;
        align-items: center;
        padding-left: 10px;
    }

    .suffix-icon-success {
        color: #67c23a;
    }

    .suffix-icon-error {
        color: #f56c6c;
    }
}
</style>



