<template>
    <pt-layout fixed-block-basis="40px" verticalMode>
        <div class="layout-header" slot="first">
        </div>
        <pt-layout fixed-block-basis="220px" slot="second">
            <layout-menu slot="first" />
            <router-view slot="second" />
        </pt-layout>
    </pt-layout>
</template>

<script>
export default {
    name: 'Layout',
};
</script>

<style lang="scss" scoped>
.layout-main {
    flex: 1;
    display: flex;
    flex-direction: row;
}
.content-main {
    flex: 1;
    display: flex;
    padding: 10px 10px 0 10px;
    overflow: hidden;
}
.layout-header {
    flex: 0 0 100%;
    display: flex;
    background: $ptit-secondary-darker;
}
</style>



