import Excel from '../views/excel';
import Search from '../views/search';

const routes = [
    {
        path: '/excelTest',
        component: Excel,
        name: 'ExcelTest',
    },
    {
        path: '/searchTest',
        component: Search,
        name: 'SearchTest',
    },
];

export default routes;



