import ReceiptList from '../views/receipt-list';
import ReceiptDetail from '../views/receipt-detail';

const routes = [
    {
        path: '/receipt-list',
        component: ReceiptList,
        name: 'ReceiptList',
    },
    {
        path: '/receipt-list/:id',
        component: ReceiptDetail,
        name: 'ReceiptDetail',
    },
];

export default routes;



