<template>
    <div class="document-list-item" @mouseenter="handleMouseenter" @mouseleave="handleMouseleave">
        <div class="document-body">
            <div class="body-left">
                <div class="row-box" @click="goDetail">
                    <pt-title secondary type="text" size="12" class="key">
                        标题：
                    </pt-title>
                    <pt-title :style="{ 'color': statusColor }" important size="5"
                        type="title" class="value ">
                        <span class="title">{{ title }}</span>
                        <pt-title size="12" type="text" format="date" class="value">
                            【
                            {{ item.receiptCode }}
                            】
                        </pt-title>
                    </pt-title>

                </div>
                <div class="row-box">
                    <pt-title secondary type="text" size="12" class="key">
                        概要：
                    </pt-title>
                    <pt-title important minor type="title" size="7" class="value">
                        <template v-if="summary.length">
                            {{ `共${summary.length}辆车` }}
                            <pt-title size="12" type="text" format="date" class="value">
                                （
                                {{ `${summary.slice(0, 5).join(',')}等` }}
                                ）
                            </pt-title>
                        </template>
                        <template v-else>
                            单据内无车辆信息
                        </template>
                    </pt-title>
                </div>
            </div>
            <div class="middle">
                <div v-if="isHover" class="document-operation">
                    <!-- <el-button type="mini" class="btn">
                        查看
                    </el-button> -->
                    <el-button v-if="item.status === 'draft'" type="mini" class="btn"
                        @click.stop="onDeleteDocument(item)">
                        删除
                    </el-button>
                </div>
            </div>
            <hr :style="{ 'background-color': statusColor }" class="separation">
            <div class="body-right">
                <div class="position">
                    <div class="row-box">
                        <pt-title secondary type="text" size="12" class="key">
                            类型：
                        </pt-title>
                        <pt-title type="text" size="14" class="value">
                            {{ item.typeName }}
                        </pt-title>
                    </div>
                    <div class="row-box">
                        <pt-title secondary type="text" size="12" class="key">
                            状态：
                        </pt-title>
                        <pt-title :style="{ 'color': statusColor }" type="text"
                            size="14" class="value">
                            {{ status }}
                        </pt-title>
                    </div>
                    <div class="row-box">
                        <pt-title secondary type="text" size="12" class="key">
                            更新时间：
                        </pt-title>
                        <pt-title type="text" size="14" class="value">
                            {{ time }}
                        </pt-title>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'DocumentListItem',
    props: {
        item: { type: Object, required: null },
    },
    data() {
        return {
            isHover: false,
        };
    },
    computed: {
        title: {
            get() {
                return this.item.title;
            },
        },
        time: {
            get() {
                return this.item.updateTime;
            },
        },
        status() {
            switch (this.item.status) {
                case 'draft':
                    return '草稿';
                case 'confirmed':
                    return '已确认';
                default:
                    return '';
            }
        },
        statusColor() {
            switch (this.item.status) {
                case 'draft':
                    return '#2c96fd"';
                case 'confirmed':
                    return 'green';
                default:
                    return '';
            }
        },
        summary: {
            get() {
                return this.item.summary ? this.item.summary.split(',') : [];
            },
        },
    },
    methods: {
        handleMouseenter() {
            this.isHover = true;
        },
        handleMouseleave() {
            this.isHover = false;
        },
        onDeleteDocument(item) {
            this.$confirm('此删除操作将永久删除文档，是否继续？', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
            })
                .then(() => {
                    this.$emit('deleteDocument', item);
                })
                .catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除',
                    });
                });
        },
        goDetail() {
            this.$emit('goDetail', this.item);
        },
    },
};
</script>
<style lang="scss" scoped>
.document-list-item {
    display: flex;
    padding: 10px;
    margin: 20px 10px 20px 0;
    background-color: #fff;
    border: 2px solid #ffd373;

    &:hover {
        background-color: #fffaee;
        box-shadow: 0 0 5px rgba($color: #000000, $alpha: 0.1);
    }

    .document-body {
        flex: auto;
        display: flex;
        align-items: center;

        .body-left,
        .body-right {
            flex: auto;
            display: flex;
            flex-flow: column nowrap;
            justify-content: center;

            .row-box {
                flex: none;
                display: flex;
                font-size: 14px;
                font-weight: 600;
                padding: 5px 5px 5px 5px;

                .key,
                .value {
                    display: flex;
                    justify-content: flex-start;
                    align-items: center;
                    white-space: nowrap;
                }

                .key {
                    flex: 0 0 60px;
                }

                .value {
                    flex: auto;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
            }
        }

        .middle {
            flex: none;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-right: 10px;
            span {
                font-size: 12px;
                font-weight: 600;
                // letter-spacing: 2px;
            }

            .document-operation {
                display: flex;
                flex-flow: row wrap;
                justify-content: center;
                align-items: center;

                .btn {
                    margin: 10px 5px;
                }
            }
        }

        .body-right {
            flex: 0 0 256px;
            align-items: center;

            .position {
                flex: none;
                display: flex;
                flex-flow: column nowrap;
                justify-content: center;
            }
        }

        .separation {
            flex: 0 0 6px;
            height: 80%;
            border: none;
            background-color: rgb(221, 221, 221);
            margin: 0 0 0 0;
        }
    }
}
.title {
    text-decoration: underline;
    cursor: pointer;
}
</style>



