import _ from 'lodash';
import Field from './field';

export default class BusinessFieldDecortor extends Field {
    constructor(field, options) {
        super(field.name, field.label, field.excelKey, field.fieldType, field.tags, field.options);
        this._overrideOptions = options;
        this._innerField = field;
    }

    get type() {
        return 'business';
    }

    get innerField() {
        return this._innerField;
    }

    get children() {
        return this.innerField.children || [];
    }

    findField(name) {
        return this.innerField.findField(name);
    }

    defineCarProperty(car, data, business) {
        return this._innerField.defineCarProperty(car, data, business);
    }

    getOption(name, defaultValue) {
        const value = this._overrideOptions[name];
        return _.defaultTo(value, super.getOption(name, defaultValue));
    }
}



