<template>
    <pt-layout fixed-block-basis="60px" verticalMode grow-block-padding="10px">
        <div class='flex-row gutter-sm header' slot="first">
            <div class='col navigation' @click="handleRouterBack">
                <el-button type="text" class="navigation-back-arrow"><i class="fa fa-arrow-left"></i></el-button>
            </div>
            <div class="col">
                <slot name="header">
                    <pt-title type="title" size="4">{{ title }}</pt-title>
                </slot>
            </div>
            <div class="col grow middle">
                <slot name="middle-area"></slot>
            </div>
            <div class="col right" :style="{flex: functionAreaFlexStyle}">
                <slot name="right-area"></slot>
            </div>
        </div>
        <slot slot="second" name="content"></slot>
    </pt-layout>
</template>

<script>
export default {
    name: 'NavigateLayout',
    props: {
        functionAreaFlexStyle: { type: String, default: '0 0 200px' },
        title: { type: String, default: '' },
    },
    methods: {
        handleRouterBack() {
            this.$router.back();
        },
    },
};
</script>


<style lang="scss" scoped>
.header {
    background-color: $ptit-grey-lighter;
    .navigation {
        flex: 0 0 20px;

        .navigation-back-arrow {
            margin-left: 10px;
        }
    }
}
</style>



