<template>
    <navigate-layout v-if="receiptManager">
        <template slot="header">
            <pt-title type="title" size="4">{{ title }}</pt-title>
        </template>
        <template slot="right-area">
            <el-button v-if="draftStatus" type="primary" size="mini" class="btn"
                @click="onComfirmReceipt">
                确认单据
            </el-button>
            <el-dropdown v-if="!draftStatus && receiptDetail && receiptDetail.type === 'inbound'"
                @command="onGenerateReceipt" class="btn">
                <el-button type="primary" size="mini">
                    生成单据
                    <i class="el-icon-arrow-down el-icon--right"></i>
                </el-button>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item command="outbound">出库确认单</el-dropdown-item>
                    <el-dropdown-item command="import_declaration">进口报关单</el-dropdown-item>
                    <el-dropdown-item command="import_certificate">进口证明书</el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
            <!-- <el-button type="primary" size="mini" class="btn" @click="backToList">
                返回列表
            </el-button> -->
        </template>
        <div slot="content" class="receipt-detail">
            <pt-layout :fixedBlockVisible="isShowCarInfoDetail" fixed-block="second"
                fixed-block-basis="33%">
                <pt-layout slot="first" :class="isShowCarInfoDetail ? 'detail' : ''"
                    verticalMode fixed-block-basis="100px">
                    <template slot="first">
                        <pt-layout fixed-block="second" class="receipt-row">
                            <div slot="first" class="left">
                                <el-input v-if="draftStatus" v-model="receiptDetail.title"
                                    class="title-input" @blur="onSaveTitle" />
                                <pt-title v-else important type="title" size="4">
                                    {{ receiptDetail ? receiptDetail.title : ''
                                    }}
                                </pt-title>
                            </div>
                            <div slot="second" class="right">
                                <pt-title important secondary type="text" size="16">
                                    {{ status }}
                                </pt-title>
                            </div>
                        </pt-layout>
                        <pt-layout fixed-block="second" class="receipt-row">
                            <template slot="first">
                                <div class="item">
                                    <pt-title minor type="text" size="14">
                                        单据类型：
                                    </pt-title>
                                    <pt-title important type="text" size="14">
                                        {{ receiptDetail ?
                                        receiptDetail.typeName : ''}}
                                    </pt-title>
                                </div>
                                <div class="item">
                                    <pt-title minor type="text" size="14">
                                        更新时间：
                                    </pt-title>
                                    <pt-title important type="text" size="14">
                                        {{ receiptDetail ?
                                        receiptDetail.updateTime : '' }}
                                    </pt-title>
                                </div>
                                <div class="item">
                                    <pt-title minor type="text" size="14">
                                        单据编号：
                                    </pt-title>
                                    <pt-title important type="text" size="14">
                                        {{ receiptDetail ?
                                        receiptDetail.receiptCode : ''}}
                                    </pt-title>
                                </div>
                            </template>
                        </pt-layout>
                    </template>
                    <el-tabs slot="second" v-model="activeName">
                        <el-tab-pane label="车辆信息" name="cars" class="tab-pane">
                            <pt-layout verticalMode fixed-block-basis="50px"
                                class="tab-pan-list">
                                <div v-if="draftStatus && activeName === 'cars'"
                                    slot="first" class="flex-row receip-button-list">
                                    <div class="col grow">
                                        <template v-if="batchButtonShow">
                                            <el-button size="mini" type="primary"
                                                @click="onBatchAction">批量操作</el-button>
                                        </template>
                                        <template v-if="draftStatus && batchAction">
                                            <el-button size="mini" type="danger"
                                                @click="onBatchDelete">删除</el-button>
                                            <el-button size="mini" type="primary"
                                                @click="onCancelBatchAction">取消</el-button>
                                        </template>
                                    </div>
                                    <div class="col right">
                                        <el-button size="mini" type="primary"
                                            @click="onShowInsertDialog">添加车辆</el-button>
                                        <car-excel-importer :business="receiptManager.listBusiness"
                                            class="excel-button" v-if="showImporter"
                                            @importCompleted="onCarImported"></car-excel-importer>

                                    </div>
                                </div>
                                <cars-info-list v-if="receiptManager" slot="second"
                                    :manager="receiptManager" :editMode="editMode"
                                    :fileNameBind="fileNameBind" mode="dialog"
                                    class="car-info-list" @carInfoSelect="onCarInfoSelect"
                                    @deleteCarInfo="deleteCarInfo" />
                            </pt-layout>
                        </el-tab-pane>
                        <template v-if="receiptManager && receiptManager.receiptDetail && fileShow">
                            <el-tab-pane v-for="item in receiptManager.receiptDetail.files"
                                :key="item.name" :label="item.labelName" :name="item.name"
                                class="tab-pane">
                                <file-list :file-list="item" :receipt-detail="receiptManager.receiptDetail"
                                    :manager="receiptManager" @deleteFile="deleteFile"
                                    @fileToCar="fileToCar" @cleaAllrFiles="cleaAllrFiles"
                                    :file-compare-car="fileCompareCar"
                                    :receipt-status="draftStatus"></file-list>
                            </el-tab-pane>
                        </template>

                    </el-tabs>
                </pt-layout>
                <car-info-detail slot="second" :car-info="detailCarLine"
                    :deletable="editMode" :business="receiptManager.detailBusiness"
                    :validator="receiptManager.detailValidator" @onShowDetail="onShowCarInfoDetail"
                    @deleteCarInfo="deleteCarInfo" />

            </pt-layout>
            <car-list-dialog :manager="receiptManager" :visible.sync="insertDialog.show"
                :business="receiptManager.listBusiness" :fields="receiptManager.listFields"
                :lines="insertDialog.lines" @dialogOk="confirmDialog" :mode="insertDialog.mode"
                @onSelectSearchInfo="onSelectSearchInfo" show-status>
            </car-list-dialog>
        </div>
    </navigate-layout>
</template>

<script>
import _ from 'lodash';
import ReceiptManager from '../js/receipt-manager';
import Configuration from '../../module-car-info/config/configuration';
import CarInfoApi from '../../api/module-car-info/car-info-api';
import CarLine from '../../module-car-info/models/car-line';

export default {
    name: 'ReceiptDetail',
    data() {
        return {
            receiptManager: ReceiptManager.instance,
            activeName: 'cars',
            inboundCustomsDeclarationBusiness: Configuration.InboundCustomsDeclaration,
            inboundCertificateReceiptBusiness: Configuration.InboundCertificateReceipt,
            insertDialog: {
                show: false,
                lines: [],
                mode: 'carsExcel',
                business: {},
            },
        };
    },
    created() {
        ReceiptManager.createInstance()
            .loadReceiptDetail(this.$route.params.id)
            .then(() => {
                this.receiptManager = ReceiptManager.instance;
                this.receiptManager.onCancelDialogBatchAction();
                this.receiptManager.onCancelBatchAction();
            });
    },
    computed: {
        title: {
            get() {
                return '单据详情';
            },
        },
        showImporter() {
            return this.fileShow;
        },
        detailCarLine: {
            get() {
                return this.receiptManager.detailCarLine;
            },
        },
        carsInfoList: {
            get() {
                return this.receiptManager ? this.receiptManager.carsInfoList : [];
            },
        },
        receiptDetail: {
            get() {
                return this.receiptManager ? this.receiptManager.receiptDetail : {};
            },
        },
        fileShow() {
            return this.receiptDetail.type !== 'inbound' && this.receiptDetail.type !== 'outbound';
        },
        status: {
            get() {
                switch (this.receiptDetail ? this.receiptDetail.status : '') {
                    case 'draft':
                        return '草稿';
                    case 'confirmed':
                        return '已确认';
                    default:
                        return '';
                }
            },
        },
        editMode: {
            get() {
                switch (this.receiptDetail ? this.receiptDetail.status : '') {
                    case 'draft':
                        return true;
                    case 'confirmed':
                        return false;
                    default:
                        return false;
                }
            },
        },
        batchAction: {
            get() {
                return this.receiptManager ? this.receiptManager.batchAction : false;
            },
        },
        draftStatus: {
            get() {
                return this.receiptDetail && this.receiptDetail.status === 'draft';
            },
        },
        batchButtonShow() {
            return this.draftStatus && !this.batchAction && this.carsInfoList.length;
        },
        isShowCarInfoDetail: {
            get() {
                return this.receiptManager.isShowCarInfoDetail;
            },
        },
    },
    methods: {
        async onCarImported(lines) {
            this.insertDialog.lines = [];
            this.insertDialog.lines = await this.compareStatus(lines);
            this.insertDialog.mode = 'carsExcel';
            this.insertDialog.show = true;
        },
        onShowInsertDialog() {
            this.insertDialog.lines = [];
            this.insertDialog.mode = 'selectCar';
            this.insertDialog.show = true;
        },
        confirmDialog() {
            const lines = _.filter(this.insertDialog.lines, r => r.status === 1 || r.status === -1);
            this.receiptManager.setFileReceiptCode(lines);
            this.insertDialog.show = false;
            this.insertDialog.lines = [];
            this.$message({
                type: 'success',
                message: '导入成功！',
            });
        },
        // backToList() {
        //     this.$router.back();
        // },
        onCarInfoSelect(item) {
            this.receiptManager.onCarInfoSelect(item);
            this.goCarInfoDetail(item);
        },
        fileToCar(vId) {
            this.activeName = 'cars';
            this.receiptManager.fileToCar(vId);
        },
        fileNameBind(field, carLine) {
            const index = _.findIndex(
                this.receiptManager.receiptDetail.files[0].files,
                file => file.receiptCode === carLine.car.getValue(field.name) && carLine.car.vId === file.vId,
            );
            if (index > -1) {
                carLine.confirmCheck = true;
                return {
                    icon: 'el-icon-success',
                    iconType: 'success',
                    message: '已匹配',
                };
            }
            carLine.confirmCheck = false;
            return {
                icon: 'el-icon-error',
                iconType: 'error',
                message: '无匹配',
            };
        },
        fileCompareCar(file) {
            return (
                _.findIndex(
                    this.receiptManager.carsInfoList,
                    carLine =>
                        file.receiptCode === carLine.car.getValue(this.receiptDetail.type) &&
                        carLine.car.vId === file.vId,
                ) > -1
            );
        },
        deleteCarInfo(item) {
            this.$confirm('是否从单据中删除该车辆？', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
            }).then(() => {
                this.receiptManager
                    .deleteCarInfo([item])
                    .then(() => {
                        this.$message({
                            type: 'success',
                            message: '删除成功！',
                        });

                        this.emptyListCancelBatchAction();
                    })
                    .catch(() => {
                        this.$message({
                            type: 'error',
                            message: '删除失败！',
                        });
                    });
            });
        },
        cleaAllrFiles(list) {
            this.$confirm('是否从单据中删除该附件？', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
            }).then(() => {
                this.receiptManager
                    .deleteFile(list.files)
                    .then(() => {
                        list.files = [];
                        this.$message({
                            type: 'success',
                            message: '删除成功！',
                        });
                    })
                    .catch(() => {
                        this.$message({
                            type: 'error',
                            message: '删除失败！',
                        });
                    });
            });
        },
        deleteFile(item, list) {
            this.$confirm('是否从单据中删除该附件？', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
            }).then(() => {
                this.receiptManager
                    .deleteFile([item])
                    .then(() => {
                        list.removeFileItem(item, list.files);
                        this.$message({
                            type: 'success',
                            message: '删除成功！',
                        });

                        this.emptyListCancelBatchAction();
                    })
                    .catch(() => {
                        this.$message({
                            type: 'error',
                            message: '删除失败！',
                        });
                    });
            });
        },
        onComfirmReceipt() {
            if (!this.carsInfoList.length) {
                this.$message({
                    type: 'warning',
                    message: '无车辆信息的单据不可确认',
                });

                return;
            }

            let shouldConfirm = false;

            _.each(this.carsInfoList, carInfo => {
                if (!carInfo.validator.validate() && shouldConfirm) {
                    shouldConfirm = true;
                }
            });

            if (this.receiptDetail.type === 'import_declaration' || this.receiptDetail.type === 'import_certificate') {
                shouldConfirm = !_.some(this.carsInfoList, carLine => !carLine.confirmCheck);
            }

            if (!shouldConfirm) {
                this.$message({
                    type: 'error',
                    message: '存在不匹配的车辆',
                });

                return;
            }

            if (this.carsInfoList.length === 0) {
                this.$message({
                    type: 'error',
                    message: '车辆不能为空',
                });
                return;
            }

            this.$confirm('确认操作不可逆转，是否确认单据？', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
            })
                .then(() => {
                    this.receiptManager
                        .onComfirmReceipt()
                        .then(() => {
                            this.receiptManager.loadReceiptDetail(this.receiptManager.receiptDetail.id);
                        })
                        .catch(() => {
                            this.$message({
                                type: 'error',
                                message: '确认失败',
                            });
                        });
                })
                .catch(() => {});
        },
        onDialogCarInfoSelect(item) {
            this.receiptManager.onCarInfoSelect(item, 'dialog');
        },
        deleteDialogCarInfo(item) {
            this.$confirm('是否确认删除？', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
            }).then(() => {
                this.receiptManager
                    .deleteCarInfo([item], 'dialog')
                    .then(() => {
                        this.$message({
                            type: 'success',
                            message: '删除成功！',
                        });

                        this.emptyListCancelBatchAction('dialog');
                    })
                    .catch(() => {
                        this.$message({
                            type: 'error',
                            message: '删除失败！',
                        });
                    });
            });
        },
        onBatchAction() {
            this.receiptManager.onBatchAction();
        },
        onCancelBatchAction() {
            this.receiptManager.onCancelBatchAction();
        },
        onBatchDelete() {
            if (!this.receiptManager.isHasChecked) {
                this.$message({
                    type: 'info',
                    message: '无可删除项！',
                });
            } else {
                this.$confirm('是否从单据中删除所选车辆？', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                }).then(() => {
                    this.receiptManager
                        .deleteCarInfo(this.receiptManager.fetchCheckedCarInfo())
                        .then(() => {
                            this.$message({
                                type: 'success',
                                message: '删除成功！',
                            });

                            this.emptyListCancelBatchAction();
                        })
                        .catch(() => {
                            this.$message({
                                type: 'error',
                                message: '删除失败！',
                            });
                        });
                });
            }
        },
        onDialogBatchAction() {
            this.receiptManager.onDialogBatchAction();
        },
        onCancelDialogBatchAction() {
            this.receiptManager.onCancelDialogBatchAction();
        },
        onGenerateReceipt(type) {
            if (!this.receiptManager.checkedItemCanShouldGenerateReceipt(type)) {
                this.$message({
                    type: 'info',
                    message: '无可生成项！',
                });

                return;
            }

            this.$confirm('确认生成单据？', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'info',
            }).then(() => {
                this.receiptManager
                    .onGenerateReceipt(type)
                    .then(uid => {
                        this.$message({
                            type: 'success',
                            message: '生成成功！',
                        });

                        this.$router.push(`/receipt-list/${uid}`);
                        this.receiptManager.loadReceiptDetail(uid);
                    })
                    .catch(() => {
                        this.$message({
                            type: 'error',
                            message: '生成失败！',
                        });
                    });
            });
        },
        onDialogBatchDelete() {
            if (!this.receiptManager.isDialogHasChecked) {
                this.$message({
                    type: 'info',
                    message: '无可删除项！',
                });
            } else {
                this.$confirm('是否确认删除？', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                }).then(() => {
                    this.receiptManager
                        .deleteCarInfo(this.receiptManager.fetchCheckedCarInfo('dialog'), 'dialog')
                        .then(() => {
                            this.$message({
                                type: 'success',
                                message: '删除成功！',
                            });

                            this.emptyListCancelBatchAction('dialog');
                        })
                        .catch(() => {
                            this.$message({
                                type: 'error',
                                message: '删除失败！',
                            });
                        });
                });
            }
        },
        emptyListCancelBatchAction(type) {
            const list = this.receiptManager.fetchListByType(type);

            if (!list.length) {
                if (type === 'dialog') {
                    this.receiptManager.onCancelDialogBatchAction();
                } else {
                    this.receiptManager.onCancelBatchAction();
                }
            }
        },
        onSaveTitle() {
            this.receiptManager.onSaveTitle();
        },
        onShowCarInfoDetail(visible) {
            this.receiptManager.onShowCarInfoDetail(visible);
        },
        goCarInfoDetail(item) {
            this.receiptManager.goCarInfoDetail(item);
            this.onShowCarInfoDetail(true);
        },
        async compareStatus(lines) {
            let business;
            switch (this.receiptDetail.type) {
                case 'import_declaration':
                    business = this.inboundCustomsDeclarationBusiness;
                    break;
                case 'import_certificate':
                    business = this.inboundCertificateReceiptBusiness;
                    break;
                default:
                    break;
            }

            const carObjIds = _.map(lines, l => l.car.vId);

            _.each(lines, l => {
                if (!_.some(this.carsInfoList, r => r.car.vId === l.car.vId)) {
                    l.status = -1;
                } else {
                    l.status = 1;
                }
            });

            const fetchCarsResult = await CarInfoApi.fetchCars(carObjIds);
            const remoteLines = _.map(fetchCarsResult, p => new CarLine(business.createCar(p), {}, {}));
            _.each(lines, l => {
                if (!_.some(remoteLines, r => r.car.vId === l.car.vId)) {
                    l.status = 2;
                } else {
                    if (_.some(remoteLines, r => r.car.vId === l.car.vId && r.car.status === '0')) {
                        l.status = 4;
                    }
                    const expectedStatus = business.getOption('statusValue', 0);
                    if (expectedStatus > 0) {
                        if (
                            _.some(
                                remoteLines,
                                r =>
                                    r.car.vId === l.car.vId &&
                                    !_.isEmpty(r.car.status) &&
                                    (r.car.status & expectedStatus) === expectedStatus,
                            )
                        ) {
                            l.status = 3;
                        }
                    }
                }
            });

            return lines;
        },
        async onSelectSearchInfo(item) {
            const lines = await this.receiptManager.onSelectSearchInfo(item);

            this.insertDialog.lines = await this.compareStatus(lines);
        },
    },
};
</script>

<style lang="scss" scoped>
.receipt-detail {
    flex: auto;
    display: flex;
    flex-flow: column nowrap;

    .detail {
        padding-right: 10px;
    }

    .tab-pane {
        display: flex;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
    }

    .receipt-row {
        margin-bottom: 10px;

        .left,
        .right,
        .item {
            display: flex;
            align-items: center;
            height: 40px;

            .title-input {
                width: 360px;
            }
        }

        .left {
            flex: auto;
            justify-content: flex-start;
        }

        .right {
            flex: none;
            justify-content: flex-end;

            .btn {
                margin-left: 10px;
            }
        }

        .item {
            margin-right: 20px;
        }
    }
}
.btn {
    margin-left: 10px;
}
.excel-button {
    margin-left: 5px;
}
</style>

<style >
.receipt-detail .el-tabs {
    flex: 1;
    display: flex;
    flex-direction: column;
}
.receipt-detail .el-tabs__content {
    flex: 1;
}
.receipt-detail .el-tab-pane {
    flex: 1;
}
.receip-button-list .el-button {
    height: 28px;
    font-size: 12px;
}
.receip-button-list {
    height: 50px;
}
</style>



