<template>
    <pt-layout fixedBlockBasis="auto" verticalMode :grow-block-scroll="true">
        <div class="content-top" slot="first">
            <grid-head-row>
                <grid-head-column v-if="batchAction" flexWidth="0 0 30px"
                    textAlign="center">
                    <el-checkbox v-model="isAllChecked" :indeterminate="isHasChecked && !isAllChecked" />
                </grid-head-column>
                <grid-head-column v-for="field in manager.listFields" v-if="!business.isHiddenField(field.name)"
                    :key="field.name" :flexWidth="field.getOption('contentWidth', '0 0 auto')"
                    :textAlign="field.getOption('contentAlign', 'center')">
                    {{field.label}}
                </grid-head-column>
                <grid-head-column flexWidth="0 0 80px" textAlign="right" />
            </grid-head-row>
        </div>
        <div slot="second">
            <template v-for="item in manager.carsInfoList">
                <cars-info-list-item :business="business" :fields="manager.listFields"
                    :key="item.car.vId" :item="item" :batch-action="batchAction"
                    :editMode="editMode" mode="list" @carInfoSelect="carInfoSelect"
                    @goToDetail="goToDetail" :fileNameBind="fileNameBind" />
            </template>
            <p v-if="manager.carsInfoList.length === 0" class="content-p">
                暂无数据
            </p>
        </div>
    </pt-layout>
</template>

<script>
export default {
    name: 'cars-info-list',
    props: {
        manager: { type: Object },
        editMode: { type: Boolean, required: false, default: false },
        fileNameBind: { type: Function },
    },
    data() {
        return {};
    },
    computed: {
        business() {
            return this.manager.listBusiness;
        },
        isAllChecked: {
            get() {
                return this.manager.isAllChecked;
            },
            set(value) {
                this.manager.isAllChecked = value;
            },
        },
        isHasChecked: {
            get() {
                return this.manager.isHasChecked;
            },
        },
        batchAction: {
            get() {
                return this.manager.batchAction;
            },
        },
        carInfoMode: {
            get() {
                return this.$route.name === 'CarsInfoList';
            },
        },
    },
    methods: {
        carInfoSelect(item) {
            this.$emit('carInfoSelect', item);
        },
        goToDetail(item) {
            this.$router.push(`/receipt-list/${item}`);
        },
    },
};
</script>

<style scoped>
.content-p {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #ccc;
    font-size: 24px;
    margin-top: 50px;
}
</style>



