import _ from 'lodash';
import ReceiptListItemModel from './receipt-list-item-model';
import FileList from './file-list';

export default class ReceiptDetail extends ReceiptListItemModel {
    carInfoList;

    constructor(data) {
        super(data);
        this.carsInfoList = data.carsInfoList;

        this._files = data.files || this.initFiles();
    }

    initFiles() {
        const carInfoFiles = new FileList('carInfoFiles', '车辆附件', []);
        const receiptFiles = new FileList('receiptFiles', '单据附件', []);

        return [carInfoFiles, receiptFiles];
    }

    initCarsAndFiles() {
        const carVIds = _.map(this.carsInfoList, r => r.car.vId);
        const fileVIds = _.map(this.files[0].files, r => r.vId);
        const commonVids = _.intersection(carVIds, fileVIds);
        _.each(this.carsInfoList, r => {
            const index = _.findIndex(this.files[0].files, k => k.vId === r.car.vId);
            if (index > -1) {
                r.car[this.type] = this.files[0].files[index].receiptCode;
            }
        });

        _.each(this.files[0].files, r => {
            const index = _.findIndex(commonVids, k => k === r.vId);
            if (index > -1) {
                r.fileCheck = true;
            }
        });
    }

    getFieldType(type) {
        switch (type) {
            case 'import_declaration':
                return 'inbound-custom-receipt';
            case 'import_certificate':
                return 'inbound-certificate-receipt';
            default:
                return '';
        }
    }

    get carsInfoList() {
        return this._carsInfoList;
    }
    set carsInfoList(value) {
        this._carsInfoList = value;
    }

    set files(val) {
        this._files = val;
    }
    get files() {
        return this._files;
    }
}



