<template>
    <div id="app">
        <router-view />
    </div>
</template>

<script>
export default {
    name: 'App',
};
</script>

<style lang="scss">
html,
body,
#app {
    height: 100%;
    width: 100%;
    display: flex;
    margin: 0;
    padding: 0;
}

/*flex layout*/
.flex-row {
    display: flex;
    display: -webkit-flex;
    flex-direction: row;
    flex: 1;

    > .col {
        display: flex;
        display: -webkit-flex;
        align-items: center;

        .el-button {
            margin-left: 5px;

            &:first-child {
                margin-left: 0;
            }
        }
    }

    > .grow {
        flex-grow: 1;
    }

    > .middle {
        justify-content: center;
    }

    > .right {
        justify-content: flex-end;
    }
}

.flex-row.gutter-sm {
    > .col {
        margin: 5px 10px;
    }
}
.flex-row.gutter-md {
    > .col {
        margin: 10px 15px;
    }
}

.flex-row.gutter-lg {
    > .col {
        margin: 15px 20px;
    }
}
</style>



