import Vue from 'vue';
import Router from 'vue-router';
import Layout from '../module-layout/views/layout';
import Cars from '../module-car-info/router';

import Receipt from '../module-receipt/router';

import Tests from '../module-tests/router';

Vue.use(Router);

const MainChildren = [...Cars, ...Receipt, ...Tests];
export default new Router({
    routes: [
        {
            path: '/',
            redirect: '/carsInfoList',
            component: Layout,
            children: MainChildren,
        },
    ],
});



