import _ from 'lodash';
import ResourceUtil from '../../api/module-resource/resource-util';
import FileItem from './file-item';

export default class FileList {
    constructor(name, labelName, files) {
        this.name = name;
        this.labelName = labelName;
        this.files = files;
    }

    createItem(data) {
        return new FileItem(data);
    }

    ifHasItem(item, list) {
        return _.findIndex(list, r => r.vId === item.vId && r.receiptCode === item.receiptCode) > -1;
    }

    getResource(uid) {
        return ResourceUtil.getResourceDetail(uid);
    }

    addFileItem(fileItem) {
        const item = new FileItem(fileItem);
        this.files.push(item);
        return item;
    }

    removeFileItem(fileItem, list) {
        list.splice(list.indexOf(fileItem), 1);
    }

    onFileSelect = (item, files) => {
        _.each(files, k => {
            k.isSelect = false;
        });

        item.isSelect = true;
    };
}



