import moment from 'moment';

export default class ReceiptListItemModel {
    _data;
    _id;
    _updateTime;
    _status;
    _title;
    _type;
    _typeName;
    _summary;

    constructor(data) {
        this._data = data;
        this._id = data.uid;
        this._updateTime = moment(data.updateTime).format('YYYY-MM-DD');
        this._status = data.status;
        this._title = data.title;
        this._type = data.type;
        this._typeName = data.typeName;
        this._summary = data.summary;
    }

    static init = data => {
        const receiptListItemModel = new ReceiptListItemModel(data);

        return receiptListItemModel;
    };

    get id() {
        return this._id;
    }
    set id(value) {
        this._id = value;
    }

    get receiptCode() {
        return this._data.receiptCode;
    }
    set receiptCode(value) {
        this._data.receiptCode = value;
    }

    get updateTime() {
        return this._updateTime;
    }
    set updateTime(value) {
        this._updateTime = value;
    }

    get status() {
        return this._status;
    }
    set status(value) {
        this._status = value;
    }

    get title() {
        return this._title;
    }
    set title(value) {
        this._title = value;
    }

    get type() {
        return this._type;
    }
    set type(value) {
        this._type = value;
    }

    get typeName() {
        return this._typeName;
    }
    set typeName(value) {
        this._typeName = value;
    }

    get summary() {
        return this._summary;
    }
    set summary(value) {
        this._summary = value;
    }

    get sortNum() {
        let statusNum;

        if (this.status === 'confirmed') {
            statusNum = '1';
        } else if (this.status === 'draft') {
            statusNum = '2';
        }

        const updateTimeNum = moment(this._data.updateTime).format(
            'YYYYMMDDHHmmss',
        );

        return Number.parseInt(statusNum + updateTimeNum, 10);
    }

    toData = () => {
        const data = this._data;

        return data;
    };
}



