export default class FileModel {
    constructor(data) {
        this._fileName = data;
        this._prepend = this._fileName.substring(0, this._fileName.indexOf('.'));
        this._prependParts = this._prepend.split('_');
        this._append = this._fileName.substring(this._fileName.indexOf('.'));

        this.isSelect = false;
        this.isChecked = false;
        this.fileCheck = false;
    }

    get fileType() {
        return this._prependParts[0] || '';
    }

    get vId() {
        return this._prependParts[1] || '';
    }

    get receiptCode() {
        return this._prependParts[2] || '';
    }

    get fileName() {
        return this._fileName;
    }
}



