import _ from 'lodash';
import moment from 'moment';
import ResourceUtil from '../../api/module-resource/resource-util';
import ReceiptListItemModel from '../models/receipt-list-item-model';
import CarInfoApi from '../../api/module-car-info/car-info-api';
import ReceiptDetail from '../models/receipt-detail';
import FileItem from '../models/file-item';
import FileList from '../models/file-list';
import ResourceStore from '../js/resource-store';
import CarLine from '../../module-car-info/models/car-line';
import Configuration from '../../module-car-info/config/configuration';

export default class ReceiptManager {
    receiptList;
    receiptDetail;
    newCarsInfoList;
    batchAction;
    dialogBatchAction;

    constructor() {
        this.receiptList = [];
        this.receiptDetail = null;
        this.newCarsInfoList = [];
        this.carsInfoListfromExcel = [];
        this.batchAction = false;
        this.dialogBatchAction = false;

        this.operationToken = '';
        this.resourceStore = new ResourceStore();
        this._business = Configuration.Inbound;
        this._listFields = this._business.getOrderedFieldsByOption('listOrders', Infinity);

        this._isShowCarInfoDetail = false;
        this._detailBusiness = Configuration.CarDetail;
        this._detailCarLine = new CarLine(this._detailBusiness.createCar({}), {});
        this._detailValidator = {};
    }

    static instance = null;

    static createInstance = () => {
        if (!ReceiptManager.instance || _.isEmpty(ReceiptManager.instance)) {
            const receiptManager = new ReceiptManager();

            ReceiptManager.instance = receiptManager;
        }

        return ReceiptManager.instance;
    };

    get listFields() {
        return this._listFields;
    }

    get listBusiness() {
        return this._business;
    }

    get isAllChecked() {
        return (
            this.receiptDetail.carsInfoList.length &&
            !_.some(this.receiptDetail.carsInfoList, carinfo => !carinfo.isChecked)
        );
    }
    set isAllChecked(value) {
        _.each(this.receiptDetail.carsInfoList, carinfo => {
            carinfo.isChecked = value;
        });
    }

    get isHasChecked() {
        return _.some(this.receiptDetail.carsInfoList, carinfo => carinfo.isChecked);
    }

    get isDialogAllChecked() {
        return this.newCarsInfoList.length && !_.some(this.newCarsInfoList, carinfo => !carinfo.isChecked);
    }
    set isDialogAllChecked(value) {
        _.each(this.newCarsInfoList, carinfo => {
            carinfo.isChecked = value;
        });
    }

    get isDialogHasChecked() {
        return _.some(this.newCarsInfoList, carinfo => carinfo.isChecked);
    }

    get carsInfoList() {
        return this.receiptDetail ? this.receiptDetail.carsInfoList : [];
    }
    set carsInfoList(value) {
        if (this.receiptDetail) {
            this.receiptDetail.carsInfoList = value;
        }
    }

    get isShowCarInfoDetail() {
        return this._isShowCarInfoDetail;
    }
    set isShowCarInfoDetail(value) {
        this._isShowCarInfoDetail = value;
    }

    get detailBusiness() {
        return this._detailBusiness;
    }
    set detailBusiness(value) {
        this._detailBusiness = value;
    }

    get detailCarLine() {
        return this._detailCarLine;
    }
    set detailCarLine(value) {
        this._detailCarLine = value;
    }

    get detailValidator() {
        return this._detailValidator;
    }

    onShowCarInfoDetail = visible => {
        if (this.batchAction && visible) {
            return;
        }

        this.isShowCarInfoDetail = visible;
    };

    loadReceiptList = async () => {
        this.onShowCarInfoDetail(false);
        const resources = await ResourceUtil.queryResourceByKey('carsReceipt', {
            value: 'receipt',
        });

        const receiptList = _.map(resources, resource => this.processReceiptResource(resource));

        this.receiptList = receiptList.sort((a, b) => b.sortNum - a.sortNum);

        return this.receiptList;
    };

    loadOneReceipt = async item => {
        const resource = await ResourceUtil.getResourceDetail(item.result);
        const receiptList = this.processReceiptResource(resource);

        this.receiptList = [receiptList];

        return this.receiptList;
    };

    processReceiptResource = resource => {
        const metadata = resource.metadata;
        const labels = metadata.labels;

        const mainLabel = _.find(labels, label => label.key === 'carsReceipt');
        const status = mainLabel.value.substring(mainLabel.value.lastIndexOf('/') + 1);
        const receiptCodeObj = _.find(labels, label => label.key === 'receiptCode');
        const titleObj = _.find(labels, label => label.key === 'title');
        const updateTimeObj = _.find(labels, label => label.key === 'updateTime');
        const typeObj = _.find(labels, label => label.key === 'type');
        const typeNameObj = _.find(labels, label => label.key === 'typeName');
        const summaryObj = _.find(labels, label => label.key === 'carIds');

        return ReceiptListItemModel.init({
            uid: metadata.uid,
            receiptCode: receiptCodeObj ? receiptCodeObj.value : '',
            status,
            title: titleObj ? titleObj.value : '',
            updateTime: updateTimeObj ? updateTimeObj.value : new Date(),
            type: typeObj ? typeObj.value : 'inbound',
            typeName: typeNameObj ? typeNameObj.value : '入库单',
            summary: summaryObj ? summaryObj.value : '',
        });
    };

    deleteReceipt = async item => {
        const resource = await ResourceUtil.getResourceDetail(item.id);

        const deleteResource = await ResourceUtil.deleteResource(item.id, resource.status.operationToken);

        this.receiptList.splice(this.receiptList.indexOf(item), 1);

        return deleteResource;
    };

    loadReceiptDetail = async id => {
        this.onShowCarInfoDetail(false);
        const resource = await ResourceUtil.getResourceDetail(id);
        this.operationToken = resource.status.operationToken;
        const receipt = this.processReceiptResource(resource);
        const carIds = receipt.summary ? receipt.summary.split(',') : [];
        const cars = carIds.length ? await CarInfoApi.fetchCars(carIds) : [];
        const carsInfoList = [];

        switch (receipt.type) {
            case 'import_declaration': {
                this._business = Configuration.InboundCustomsDeclaration;
                break;
            }
            case 'import_certificate': {
                this._business = Configuration.InboundCertificateReceipt;
                break;
            }
            case 'inbound': {
                this._business = Configuration.Inbound;
                break;
            }
            case 'outbound':
            default: {
                this._business = Configuration.Outbound;
                break;
            }
        }
        this._listFields = this._business.getOrderedFieldsByOption('listOrders', Infinity);

        _.each(cars, car => {
            carsInfoList.push(this.carsFactory(car));
        });

        carsInfoList.sort((a, b) => {
            if (a.vId < b.vId) {
                return -1;
            } else if (a.vId > b.vId) {
                return 1;
            }

            return 0;
        });

        const tmp = _.filter(resource.datas, r => r.contentType === 'text/plain');
        const carInfoFiles = _.filter(tmp, r => r.name.split('_')[0] !== 'receiptFile').map(r => new FileItem(r.name));
        const receiptFiles = _.filter(tmp, r => r.name.split('_')[0] === 'receiptFile').map(r => new FileItem(r.name));

        const files = [
            new FileList('carInfoFiles', '车辆附件', carInfoFiles),
            new FileList('receiptFiles', '单据附件', receiptFiles),
        ];

        const receiptDetail = new ReceiptDetail({
            ...receipt.toData(),
            carsInfoList,
            files,
        });

        this.receiptDetail = receiptDetail;
    };

    carsFactory(obj) {
        const car = this._business.createCar(obj);

        return new CarLine(car, {}, this._business.createValidator(car));
    }

    onCarInfoSelect = (item, type) => {
        const list = this.fetchListByType(type);

        _.each(list, carInfo => {
            carInfo.isSelect = false;
        });

        item.isSelect = true;
    };

    goCarInfoDetail = item => {
        this._detailCarLine = item;
        this._detailValidator = this._detailBusiness.createValidator(this.detailCarLine.car);
    };

    fileToCar(vId) {
        const car = _.find(this.receiptDetail.carsInfoList, k => k.car.vId === vId);
        this.onCarInfoSelect(car);
    }

    fetchListByType = type => {
        if (type === 'dialog') {
            return this.newCarsInfoList;
        }

        return this.carsInfoList;
    };

    onComfirmReceipt = async () => {
        const resourceContent = await ResourceUtil.queryResourceContent(this.receiptDetail.id, 'carsReceipt', {
            verson: 0,
        });

        _.each(resourceContent, r => {
            const value = _.find(r, k => k.key === 'vId').value;
            const index = _.findIndex(this.carsInfoList, carLine => carLine.car.vId === value);

            if (index > -1) {
                r.push({
                    key: this.receiptDetail.type,
                    value: this.carsInfoList[index].car.getValue(this.receiptDetail.type),
                });
            }
        });

        const resource = await ResourceUtil.getResourceDetail(this.receiptDetail.id);

        ResourceUtil.putJsonResource(
            'carsReceipt',
            resourceContent,
            this.receiptDetail.id,
            resource.status.operationToken,
            false,
        );

        await ResourceUtil.commitOneResouce(
            this.receiptDetail.id,
            {
                type: 'commit',
            },
            {
                headers: {
                    operationToken: resource.status.operationToken,
                },
            },
        );

        await ResourceUtil.putLabels(this.receiptDetail.id, [
            {
                key: 'carsReceipt',
                value: `receipt/${this.receiptDetail.type}/confirmed`,
            },
            {
                key: 'updateTime',
                value: moment().format('YYYY-MM-DD HH:mm:ss'),
            },
        ]);

        this.receiptDetail.status = 'comfirmed';

        return this.receiptDetail.status;
    };

    onSelectSearchInfo = async item => {
        const arrs = [];

        if (_.isArray(item.result)) {
            const result = await CarInfoApi.fetchCars(item.result);

            _.each(result, r => {
                arrs.push(this.carsFactory(r));
            });
        } else {
            const result = await CarInfoApi.fetchCars([item.result]);

            _.each(result, r => {
                arrs.push(this.carsFactory(r));
            });
        }

        this.newCarsInfoList = [];

        _.eachRight(arrs, carInfo => {
            if (!_.some(this.newCarsInfoList, car => car.car.vId === carInfo.car.vId)) {
                this.newCarsInfoList.unshift(carInfo);
            }
        });

        return this.newCarsInfoList;
    };

    onBatchAction = () => {
        this.onShowCarInfoDetail(false);
        this.batchAction = true;
    };

    onCancelBatchAction = () => {
        this.batchAction = false;

        this.clearChecked();
    };

    onDialogBatchAction = () => {
        this.dialogBatchAction = true;
    };

    onCancelDialogBatchAction = () => {
        this.dialogBatchAction = false;

        this.clearChecked('dialog');
    };

    clearChecked = type => {
        const list = this.fetchListByType(type);

        _.each(list, carInfo => {
            carInfo.isChecked = false;
        });
    };

    onGenerateReceipt = async type => this.resourceStore.onGenerateReceipt(this.receiptDetail.carsInfoList, type);

    checkedItemCanShouldGenerateReceipt(type) {
        return this.resourceStore.checkedItemCanShouldGenerateReceipt(type, this.carsInfoList);
    }

    InsertCarInfosToList = async () => {
        _.eachRight(this.newCarsInfoList, carInfo => {
            if (
                !_.some(this.carsInfoList, car => car.car.vId === carInfo.car.vId) &&
                ((this.receiptDetail.type === 'inbound' && (carInfo.car.status & 1) !== 1) ||
                    (this.receiptDetail.type !== 'inbound' &&
                        (carInfo.car.status & 16) !== 16 &&
                        (carInfo.car.status & 1) === 1))
            ) {
                this.carsInfoList.unshift(carInfo);
            }
        });

        return this.onUpdateReceiptCarInfoListResource();
    };

    onUpdateReceiptCarInfoListResource = async () => {
        const carIds = this.fetchCarsInfoListIds();

        const cars = _.map(carIds, r => [{ key: 'vId', value: r }]);

        await ResourceUtil.putLabels(this.receiptDetail.id, [
            {
                key: 'carIds',
                value: carIds.join(','),
            },
            {
                key: 'updateTime',
                value: moment().format('YYYY-MM-DD HH:mm:ss'),
            },
        ]);

        const resource = await ResourceUtil.getResourceDetail(this.receiptDetail.id);

        return ResourceUtil.putJsonResource('carsReceipt', cars, this.receiptDetail.id, resource.status.operationToken);
    };

    fetchCarsInfoListIds = () => _.map(this.carsInfoList, carInfo => carInfo.car.vId);

    deleteCarInfo = async (cars, type) => {
        const list = this.fetchListByType(type);

        _.each(cars, carInfo => list.splice(list.indexOf(carInfo), 1));

        if (!type) {
            this.onShowCarInfoDetail(false);
            this.onUpdateReceiptCarInfoListResource();
        }

        // this.receiptDetail.initCarsAndFiles().apply(this.receiptDetail);

        return list;
    };

    fetchCheckedCarInfo = type => {
        const list = this.fetchListByType(type);

        return _.filter(list, carInfo => carInfo.isChecked);
    };

    isHasShouldInbound = () => {
        const list = this.carsInfoList;

        return _.some(list, carInfo => (carInfo.car.status & 1) !== 1);
    };

    isAllShouldInbound = () => {
        const list = this.carsInfoList;

        return !_.some(list, carInfo => (carInfo.car.status & 1) === 1);
    };

    isHasShouldOutbound = () => {
        const list = this.carsInfoList;

        return _.some(list, carInfo => (carInfo.car.status & 16) !== 16 && (carInfo.car.status & 1) === 1);
    };

    onSaveTitle = async () => {
        const resource = await ResourceUtil.putLabels(this.receiptDetail.id, [
            {
                key: 'title',
                value: this.receiptDetail.title,
            },
            {
                key: 'updateTime',
                value: moment().format('YYYY-MM-DD HH:mm:ss'),
            },
        ]);

        return resource;
    };

    async deleteFile(items) {
        return Promise.all(
            _.map(items, async r => {
                const resource = await ResourceUtil.getResourceDetail(this.receiptDetail.id);
                return ResourceUtil.deleteDataName(
                    this.receiptDetail.id,
                    r.fileName,
                    {
                        refreshToken: false,
                    },
                    resource.status.operationToken,
                );
            }),
        );
    }

    onShowDetail = visible => {
        this.onShowCarInfoDetail(visible);
    };

    compareCarsFromExcelAndCurrentLines(cars) {
        return _.filter(cars, r => _.some(this.carsInfoList, k => k.car.vId === r.car.vId));
    }

    setFileReceiptCode(lines) {
        _.each(this.carsInfoList, r => {
            const lineLndex = _.findIndex(lines, k => k.car.vId === r.car.vId);
            if (lineLndex > -1) {
                r.car[this.receiptDetail.type] = lines[lineLndex].car[this.receiptDetail.type];
            }

            // const fileIndex = _.findIndex(this.receiptDetail.files[0].files, l => l.vId === r.car.vId);
            // if (fileIndex > -1) {
            //     r.car[this.receiptDetail.type] = this.receiptDetail.files[0].files[fileIndex].receiptCode;
            // }
        });

        const newCars = _.filter(lines, r => r.status === -1);
        this.carsInfoList.unshift(...newCars);

        this.onUpdateReceiptCarInfoListResource();
    }
}



