import _ from 'lodash';
import Field from './field';

export default class CompositeField extends Field {
    constructor(name, label, tags, options, children) {
        super(name, label, '$$COMPOSITE_FILE', 'COMPOSITE', tags, options);
        this._children = children;
        _.each(this._children, p => (p.parent = this));
    }

    get children() {
        return this._children;
    }

    get type() {
        return 'composite';
    }

    findField(name) {
        let field = super.findField(name);
        if (_.isEmpty(field)) {
            field = _.find(this.children, p => !_.isEmpty(p.findField(name)));
        }
        return _.defaultTo(field, {});
    }

    defineCarProperty(car, data, business) {
        super.defineCarProperty(car, data, business);
        _.each(this.children, p => p.defineCarProperty(car, data, business));
    }
}



