import _ from 'lodash';
import __ from '../../utils/__';
import CarInfoApi from '../../api/module-car-info/car-info-api';
import CarLine from '../models/car-line';

export default class Business {
    constructor(name, fields, options) {
        this._name = name;
        this._fieldsObject = _.keyBy(fields, p => p.name);
        this._excelFieldsObject = _.keyBy(fields, p => p.excelKey);
        this._fields = fields;
        this._formatValue = true;
        this._options = options || {};
    }

    get fields() {
        return this._fields;
    }

    get fieldsObject() {
        return this._fieldsObject;
    }

    get formatValue() {
        return this._formatValue;
    }

    set formatValue(value) {
        this._formatValue = value;
    }

    getOption(name, defaultValue) {
        return _.defaultTo(this._options[name], defaultValue);
    }

    get name() {
        return this._name;
    }

    isHiddenField(name, car) {
        return this.isOptionField('hidden', name, car);
    }

    isEditableField(name, car) {
        return this.isOptionField('editable', name, car);
    }

    isOptionField(optionName, fieldName, car) {
        let field = this.fieldsObject[fieldName];

        if (_.isUndefined(field)) {
            field = _.reduce(this.fields, (r, v) => (_.isEmpty(r) ? v.findField(optionName, fieldName) : r), {});
        }

        if (!_.isEmpty(field)) {
            if (_.some(field.getOption(optionName, []), p => p === this.name)) {
                if (car && this.calculateOptionField(optionName, fieldName, car)) {
                    return true;
                }

                return true;
            }
        }

        if (car && this.calculateOptionField(optionName, fieldName, car)) {
            return true;
        }

        return false;
    }

    calculateOptionField(optionName, fieldName, car) {
        if (optionName === 'hidden') {
            if (fieldName === 'inbound') {
                return !((_.defaultTo(car.getValue('status'), 0) & 1) === 1);
            } else if (fieldName === 'outbound') {
                return !((_.defaultTo(car.getValue('status'), 0) & 16) === 16);
            }
        }

        return false;
    }

    isDisableField(name, car) {
        if (name === 'vId' || name === 'eId') {
            return _.defaultTo(car.getValue('status'), 0) > 0;
        }

        let field = this.fieldsObject[name];
        if (_.isUndefined(field)) {
            field = _.reduce(this.fields, (r, v) => (_.isEmpty(r) ? v.findField(name) : r), {});
        }

        if (!_.isEmpty(field)) {
            return _.some(field.getOption('disable', []), p => p === this.name);
        }

        return false;
    }

    doNotFormatValue() {
        this.formatValue = false;
        return this;
    }

    createCar = keyValues => {
        const car = {};
        const keys = _.map(keyValues, p => p.key);
        const values = _.map(keyValues, p => p.value);
        const data = _.zipObject(keys, values);
        car._data = data;
        _.each(this.fields, f => f.defineCarProperty(car, data, this));
        const $this = this;
        car.getValue = name => {
            let value = data[name];
            if (_.isUndefined($this.fieldsObject[name])) {
                const f = _.reduce($this.fields, (r, v) => (_.isEmpty(r) ? v.findField(name) : r), {});
                if (!_.isEmpty(f) && !_.isEmpty(f.parent)) {
                    value = car.getValue(f.parent.name);
                    if (!_.isUndefined(value)) {
                        const bitValue = f.getOption('bitValue', 0);
                        value = (value & bitValue) === bitValue;
                    }
                }
            }
            return value;
        };
        car.toData = () => {
            const d = [];
            const noToDataProp = ['status', 'inbound', 'outbound', 'import_declaration', 'import_certificate'];

            _.each(Object.keys(car._data), r => {
                d.push({
                    key: r,
                    value: car._data[r],
                });
            });
            _.remove(d, item => noToDataProp.includes(item.key));

            return d;
        };

        return car;
    };

    createValidator(car) {
        const validator = {};

        _.each(this.fields, f => f.defineValidator(validator));
        return __.createModelValidator(validator)(car);
    }

    async asyncLoadByVIDs(vids) {
        const ids = _.isArray(vids) ? vids : [vids];
        const keyValues = await CarInfoApi.fetchCars(ids);
        return _.map(keyValues, p => this.createCarLine(p));
    }

    createCarLine(keyValues, fromExcel) {
        const car = fromExcel ? this.createCarFromExcel(keyValues) : this.createCar(keyValues);
        return new CarLine(car, {}, this.createValidator(car));
    }

    createCarFromExcel(excelValues) {
        const keyValues = [];
        _.forOwn(this._excelFieldsObject, p =>
            keyValues.push({
                key: p.name,
                value: excelValues[p.excelKey] ? excelValues[p.excelKey].value : '',
            }),
        );
        return this.createCar(keyValues);
    }

    getOrderedFieldsByOption(optionName, defaultValue) {
        return _.orderBy(this.fields, p => p.getOption(optionName, defaultValue));
    }

    getGroupFieldsByOption = (optionName, defaultValue) =>
        _.groupBy(this.fields, field => field.getOption(optionName, defaultValue));
}



