<template>
    <pt-layout :fixed-block-basis="fiexBlockBasis" verticalMode>
        <pt-layout slot="first" style="align-items: center;" v-if="receiptStatus">
            <div slot="first" class="upload-demo">
                <el-button type="primary" size="mini" @click="cleaAllrFiles">
                    全部清除
                </el-button>
            </div>
            <div slot="second" class="upload-demo upload-block">
                <el-upload ref="upload" :action="fileUrl" :http-request="upload"
                    :data="uploadData" :headers="headers" :on-success="handleSuccess"
                    :show-file-list="false" multiple>
                    <el-tooltip content="可批量上传附件，附件将自动与车辆进行匹配" placement="top"
                        effect="light">
                        <i class="el-icon-info" style="color: #909399;"></i>
                    </el-tooltip>
                    <el-button size="mini" type="primary" @click="getResource">上传附件</el-button>
                </el-upload>
            </div>
        </pt-layout>
        <pt-layout slot="second" verticalMode grow-block-scroll fixedBlockBasis="auto">
            <div slot="first" class="content-top">
                <grid-head-row>
                    <grid-head-column flexWidth="0 0 500px" textAlign="left">
                        附件名称
                    </grid-head-column>
                    <grid-head-column flexWidth="0 0 180px" textAlign="left">
                        VIN码
                    </grid-head-column>
                    <grid-head-column flexWidth="0 0 30px" textAlign="left">
                        匹配
                    </grid-head-column>
                </grid-head-row>
            </div>
            <div slot="second">
                <template v-for="item in fileList.files">
                    <file-list-item :item="item" :key="item.fileName"
                        @deleteFile="deleteFile" :mode="fileList.name"
                        @fileSelect="fileSelect" @fileToCar="fileToCar"
                        :fetch-suffix-icon="fileCompareCar"></file-list-item>
                </template>
                <p class="content-p" v-if="fileList.files.length === 0">
                    暂无数据
                </p>
            </div>
        </pt-layout>
    </pt-layout>

</template>

<script>
import _ from 'lodash';
import putUpload from '../../utils/put-upload';

export default {
    name: 'file-list',
    props: {
        fileList: { type: Object },
        receiptDetail: { type: Object },
        manager: { type: Object },
        fileCompareCar: { type: Function },
        receiptStatus: { type: Boolean },
    },
    data() {
        return {
            exceldata: '',
            uploadData: {
                refreshToken: false,
            },
            operationToken: '',
        };
    },
    computed: {
        fileUrl() {
            return `${serverApiConfig.resource}/v1/${this.receiptDetail.id}`;
        },
        headers() {
            return {
                operationToken: this.operationToken,
                'Ocp-Apim-Subscription-Key': 'ad91c2f95eab4af7aa1efe4adb9a6e60',
            };
        },
        fiexBlockBasis() {
            return this.receiptStatus ? '50px' : 'auto';
        },
    },
    mounted() {},
    methods: {
        cleaAllrFiles() {
            this.$emit('cleaAllrFiles', this.fileList);
        },
        fileSelect(item) {
            this.fileList.onFileSelect(item, this.fileList.files);
        },
        fileToCar(vId) {
            this.$emit('fileToCar', vId);
        },
        getResource() {
            this.fileList.getResource(this.receiptDetail.id).then(result => {
                this.operationToken = result.status.operationToken;
            });
        },
        upload(item) {
            item.data.dataName = item.file.name;
            return putUpload(item);
        },
        handleSuccess(response, file) {
            this.exceldata = response.data;
            this.compareFiles(file);
        },
        deleteFile(item) {
            this.$emit('deleteFile', item, this.fileList);
        },
        compareFiles(file) {
            const origin = this.fileList.createItem(file.name);
            const isExistence = this.fileList.ifHasItem(origin, this.fileList.files);
            if (isExistence) {
                return;
            }
            this.fileList.addFileItem(file.name);
            const target = _.find(this.receiptDetail.carsInfoList, p => p.car.vId === origin.vId);
            if (target) {
                target.car[this.receiptDetail.type] = origin.receiptCode;
            }
        },
    },
};
</script>

<style lang="scss" scoped>
.file-list {
    display: flex;
    flex-direction: column;
    flex: 1;
}
.content-list {
    flex: 1;
    overflow-y: auto;
}
.content-p {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #ccc;
    font-size: 24px;
    margin-top: 50px;
}
.upload-block {
    display: flex;
    justify-content: flex-end;
    flex: 1;

    span {
        font-size: 12px;
    }
}
</style>


<style lang="scss">
.file-list .upload-demo {
    margin-top: 4px;
    button {
        height: 32px;
    }

    span {
        font-size: 14px;
    }
}
.file-first-list {
    align-items: center;
}
</style>



