import _ from 'lodash';
import moment from 'moment';
import ResourceUtil from '../../api/module-resource/resource-util';

export default class ResourceStore {
    onGenerateReceipt = async (cars, type) => {
        let newReceipt = {};

        switch (type) {
            case 'inbound':
                newReceipt = this.createReceipt(cars, '入库确认单', 'cars-inbound', 'RK');
                break;
            case 'outbound':
                newReceipt = this.createReceipt(cars, '出库确认单', 'cars-outbound', 'CK');
                break;
            case 'import_certificate':
                newReceipt = this.createReceipt(cars, '进口证明书', 'import_certificate', 'IC');
                break;
            case 'import_declaration':
                newReceipt = this.createReceipt(cars, '进口报关单', 'import_declaration', 'ID');
                break;
            default:
                break;
        }

        const resourceParam = [
            {
                key: 'carsReceipt',
                value: `receipt/${type}/draft`,
            },
            {
                key: 'resource-type',
                value: newReceipt.resourceType,
            },
            {
                key: 'type',
                value: type,
            },
            {
                key: 'typeName',
                value: newReceipt.typeName,
            },
            {
                key: 'title',
                value: newReceipt.title,
            },
            {
                key: 'updateTime',
                value: moment().format('YYYY-MM-DD HH:mm:ss'),
            },
            {
                key: 'carIds',
                value: newReceipt.carIds.join(','),
            },
            {
                key: 'receiptCode',
                value: newReceipt.receiptCode,
            },
        ];

        const resource = await ResourceUtil.createOneResource(resourceParam);

        await ResourceUtil.putJsonResource('carsReceipt', newReceipt.carObjs, resource.uid, resource.operationToken);

        return resource.uid;
    };

    createReceipt(cars, typeName, resourceType, receiptPrepend) {
        const updateTime = moment().format('YYYY-MM-DD HH:mm:ss');
        const receiptCode = `${receiptPrepend}_${moment(updateTime).format('YYYYMMDDHHmmss')}`;

        let filterCars = [];

        switch (resourceType) {
            case 'cars-inbound':
                filterCars = _.filter(cars, carInfo => (carInfo.car.status & 1) !== 1);
                break;
            case 'cars-outbound':
                filterCars = _.filter(
                    cars,
                    carInfo => (carInfo.car.status & 16) !== 16 && (carInfo.car.status & 1) === 1,
                );
                break;
            case 'import_declaration':
                filterCars = _.filter(
                    cars,
                    carInfo => (carInfo.car.status & 4) !== 4 && (carInfo.car.status & 1) === 1,
                );
                break;
            case 'import_certificate':
                filterCars = _.filter(
                    cars,
                    carInfo => (carInfo.car.status & 8) !== 8 && (carInfo.car.status & 1) === 1,
                );
                break;
            default:
                filterCars = [];
                break;
        }

        const carIds = _.map(filterCars, carInfo => carInfo.car.vId);
        const carObjs = _.map(filterCars, carInfo => [{ key: 'vId', value: carInfo.car.vId }]);
        const title = `${typeName} ${updateTime}`;

        return {
            typeName,
            resourceType,
            receiptCode,
            carIds,
            title,
            carObjs,
        };
    }

    checkedItemCanShouldGenerateReceipt(type, list) {
        switch (type) {
            case 'inbound':
                return _.some(list, carInfo => (carInfo.car.status & 1) !== 1);
            case 'outbound':
                return _.some(list, carInfo => (carInfo.car.status & 16) !== 16 && (carInfo.car.status & 1) === 1);
            case 'import_declaration':
                return _.some(list, carInfo => (carInfo.car.status & 4) !== 4 && (carInfo.car.status & 1) === 1);
            case 'import_certificate':
                return _.some(list, carInfo => (carInfo.car.status & 8) !== 8 && (carInfo.car.status & 1) === 1);
            default:
                return false;
        }
    }
}



