<template>
    <div :class="carInfoListItemClass" @click="carInfoSelect">
        <grid-row>
            <grid-cell v-if="batchAction" flexWidth="0 0 30px" justify-content="center">
                <template name="view">
                    <div @click.stop>
                        <el-checkbox v-model="item.isChecked" />
                    </div>
                </template>
            </grid-cell>

            <grid-cell v-for="field in fields" v-if="!business.isHiddenField(field.name, item.car)"
                :key="field.name" :flexWidth="field.getOption('contentWidth', '0 0 auto')"
                :justify-content="field.getOption('contentAlign', 'center')">
                <template name="view">
                    <car-field-value :item="item" :field="field" :editMode="editMode && business.isEditableField(field.name, item.car)"
                        @iconClick="onIconClick" :fetch-suffix-icon="fileNameBind" />
                </template>
            </grid-cell>
            <slot name="line-suffix"></slot>
        </grid-row>
    </div>
</template>


<script>
import _ from 'lodash';

export default {
    name: 'cars-info-list-item',
    props: {
        fields: { type: Array, required: true },
        item: { type: Object },
        mode: { type: String },
        batchAction: { type: Boolean, required: false, default: false },
        business: { type: Object, required: true },
        editMode: { type: Boolean, required: false, default: false },
        fileNameBind: { type: Function },
        compareCars: { type: Function },
    },
    computed: {
        carInfoListItemClass: {
            get() {
                return {
                    'cars-info-list-item': true,
                    'cars-info-list-item-select': this.item.isSelect,
                };
            },
        },
        inboundParts() {
            if (!_.isEmpty(this.item.car.inbound)) {
                return this.item.car.inbound.split(',');
            }
            return ['', ''];
        },
        outboundParts() {
            if (!_.isEmpty(this.item.car.outbound)) {
                return this.item.car.outbound.split(',');
            }
            return ['', ''];
        },
        listMode: {
            get() {
                return this.mode === 'list';
            },
        },
        dialogMode: {
            get() {
                return this.mode === 'dialog';
            },
        },
        carInfoMode: {
            get() {
                return this.$route.name === 'CarsInfoList';
            },
        },
        originalStatus: {
            get() {
                return `${this.item.car.status}` === '0';
            },
        },
    },
    methods: {
        onIconClick(item, field) {
            if (field.name === 'inbound-status' && this.getStatus(1)) {
                this.goToDetail(this.inboundParts[1]);
            } else if (field.name === 'outbound-status' && this.getStatus(16)) {
                this.goToDetail(this.outboundParts[1]);
            }
        },
        getStatus(statusNum) {
            const status = [];
            if ((this.item.car.status & 16) === 16) {
                status.push(16);
            }

            if ((this.item.car.status & 1) === 1) {
                status.push(1);
            }
            if (status.length === 0) {
                status.push(0);
            }

            return _.findIndex(status, k => k === statusNum) > -1;
        },
        carInfoSelect() {
            this.$emit('carInfoSelect', this.item);
        },
        goToDetail(item) {
            this.$emit('goToDetail', item);
        },
    },
};
</script>

<style lang="scss" scoped>
.cars-info-list-item {
    display: flex;
    align-items: center;
    min-height: 40px;
    cursor: pointer;

    &:nth-child(2n + 1) {
        background-color: #faf5ed;
    }

    &:nth-child(2n) {
        background-color: #ffe5bb;
    }

    &:hover {
        background-color: #c0ccda;
    }

    .icon-btn {
        width: 30px;
        padding: 5px 5px;
    }
}

.cars-info-list-item-select {
    background-color: #9ca8b6 !important;
}
.statusUnderLine {
    text-decoration: underline;
    cursor: pointer;
}
</style>



